.brif {
  padding-bottom: 121px;

  & .works-hero {
    padding-bottom: 88px;

    &__wp {
      text-align: center;

      & h2 {
        line-height: 140%;
        margin-top: 5px;
        & br {
          display: none;
        }
      }
    }
  }

  &__container {
    max-width: 50%;
    padding-left: 200px;
    box-sizing: content-box;
  }

  &__title {
    letter-spacing: 0.02em;
    line-height: 140%;
    font-weight: 600;
    font-size: 40px;
    margin-bottom: 24px;
    font-feature-settings: 'tnum'on, 'lnum'on;
  }
}

.brif-greeting {
  padding: 120px 0px 80px;

  &__text {
    font-weight: 300;
    line-height: 180%;
    font-size: 16px;
    font-feature-settings: 'tnum'on, 'lnum'on;

    & span {
      color: #FF8F70;
    }
  }
}

.brif-form {
  & input, textarea {
    font-family: 'Manrope',sans-serif;
  }
  &__body {
    display: flex;
    flex-direction: column;
    gap: 32px;
    border-top: 2px solid #242B2F;
    padding-top: 48px;
  }
  &__row {
      &.wrong {
          & input, textarea {
              background: linear-gradient(180deg,rgba(255, 179, 179, 0.52),rgba(255, 82, 82, 0.26));
          }
          
      }
      textarea {
        display: block;
        width: 100%;
        box-sizing: border-box;
        resize: none;
        min-height: 3em;
        
      }
      
      
  }
  &__bottom {
    justify-content: end;
    display: flex;
    align-items: center;
    column-gap: 64px;
    row-gap: 17px;
    flex-wrap: wrap;
    
    & button {
      background-color: transparent;
    }
    button:disabled {
      background-color: #c0c0c04a;
      pointer-events: none; /* Отключаем взаимодействие с кнопкой */
      &::after {
        background-color: #c0c0c04a;
        }
        &::before {
          background-color: #c0c0c04a;
        }
    }
    
  }
  &__cheked {
    display: flex;
    gap: 16px;
    align-items: center;
    & p {
      font-size: 14px;
      font-weight: 300;
      line-height: 160%;
      & a {
        font-size: 14px;
        font-weight: 300;
        line-height: 160%;
        text-decoration: underline;
        @media (hover: hover) {
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
  }
  &__file {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }

  &__input-file {
    max-width: 518px;
  }
}




.brif-contacts {
  position: absolute;
  top: 0;
  // transform: translate(0px, 520px,);
  right: 12%;
  width: 22.5%;
  // transition: margin-top .3s ease-in-out;
  &__fixed {
    position: fixed;
    margin-top: 120px;/* Отступ от верхней части экрана */
    top: 0;
    /* Дополнительные стили для зафиксированного состояния */
  }
  &__active {
    top: 96px;
  }
  &__label {
    font-weight: 300;
    line-height: 140%;
    letter-spacing: 0.02em;
    margin-bottom: 24px;
    font-size: 24px;
  }

  &__body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
  }

  &__el {
    max-width: 174px;
  }

  &__tel {
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    display: flex;
    margin-bottom: 58px;
    transition: ease-in-out .25s;

    & span {
      margin-left: 14px;
      border-bottom: 2px solid #CE83EA;
      padding-bottom: 5px;
    }

    @media (hover: hover) {
      &:hover {
        color: #CE83EA;

        & span {
          border-bottom-color: #242B2F;
        }
      }

    }
  }

  &__title {
    font-weight: 300;
    line-height: 140%;
    letter-spacing: 0.02em;
  }

  &__img {
    display: block;
    margin-top: 12px;
    transition: .2s;
    padding: 16px 16px 0;
    margin-left: -16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    width: 206px;

    & img {
      width: 100%;
    }

    & span {
      transition: .2s;
      opacity: 0;
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 140%;
      text-align: center;
      display: block;
      height: 38px;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-pack: center;
      justify-content: center;
      padding-bottom: 7px;
    }

    @media (hover: hover) {
      &:hover {
        background: rgba(217, 217, 217, .4);

        & span {
          opacity: 1;

        }
      }
    }


  }

  &__img-mob {
    display: none;
  }
}

@media (min-width: 1921px) {
  .brif-contacts {
    left: 62%;
    right: 0;
    width: 431px;
  }
}

@media (max-width: 1650px) {
  .brif__container {
    max-width: 100%;
    padding-right: 200px;
  }

  .brif-contacts {
    display: block;
    padding-left: 300px;
    position: inherit;
    top: 0;
    left: 0;
    right: 0;
    width: 431px;
    box-sizing: content-box;
    padding-top: 80px;

    &__title {
      display: none;
    }

    &__img {
      display: none;
    }

    &__img-mob {
      display: flex;
      align-items: center;
      justify-content: flex-start;

      & img {
        margin-right: 10px;
      }

      & p {
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
      }
    }
  }
}

@media (max-width: $desktop) {
  .brif {
    .works-hero {
      padding-bottom: 48px;
    }
  }

  .brif-greeting {
    padding-top: 80px;
  }

  .brif__container {
    padding: 0px 100px;
  }

  .brif-contacts {
    padding-left: 200px;
  }
}

@media (max-width: $tablet) {
  .brif-greeting {
    padding-top: 60px;
  }

  .brif__container {
    padding: 0px 50px;
  }

  .brif-contacts {
    padding-left: 120px;
  }
}

@media (max-width: 768px) {
  .brif {
    .works-hero {
      padding-top: 152px;

      &__wp h2 {
        margin-top: 7px;

        & br {
          display: block;
        }
      }

      &__wp svg {
        width: 100%;
      }
    }

    &__title {
      font-size: 24px;
      margin-bottom: 19px;
    }
  }

  .brif__container {
    padding: 0px;
  }

  .brif-greeting {
    padding-top: 40px;
  }

  .brif-form {
    &__body {
      padding-top: 40px;
      gap: 24px;
    }

    // &__label {
    //   font-size: 16px;
    // }
  }

  .brif-contacts {
    // &__label {
    //   font-size: 24px;
    // }

    &__tel {
      margin-bottom: 40px;
    }
  }
}

@media (max-width: $mobile-big) {
  .brif-contacts {
    padding: 50px 32px 0px;
  }
}

@media (max-width: 760px) {
  .brif-contacts {
    padding: 40px 16px 0px;
  }
  .brif-form__bottom {
    justify-content: center;
  }
}

@media (max-width: $mobile) {
  .brif {
    & .works-hero {
      padding-top: 80px;
      padding-bottom: 24px;

      &__wp {
        & svg {
          width: 47px;
        }
      }
    }

    &__title {
      font-size: 16px;
      margin-bottom: 8px;
    }
  }

  .brif-greeting {
    padding-bottom: 64px;

    &__text {
      font-size: 12px;
    }
  }

  .brif-form {
    &__body {
      padding-top: 24px;
      gap: 16px;
    }

  }

  .brif-contacts {
    width: 100%;
    box-sizing: border-box;

    &__tel {
      margin-bottom: 0;

      & span {
        font-size: 12px;
      }
    }

    &__img-mob {
      flex-basis: 33%;

      & p {
        font-size: 12px;

      }
    }

    &__el {
      display: flex;
      max-width: 100%;
      justify-content: space-between;
      align-items: center;
      flex-basis: 100%;
    }

    &__label {
      font-size: 16px;
      margin-bottom: 16px;
    }
  }

}

@media (max-width: $mobile-sm) {
  .brif-form {
    &__input-file {
      flex-basis: 100%;
    }

    &__btn {
      margin: 0 auto;

      & .btn {
        height: 26px;
        font-size: 9px;

        &::before {
          height: 26px;
        }
      }
    }
    &__cheked {
      gap: 12px;
      & p {
        font-size: 9px;
        & a {
          font-size: 9px;
        }
      }
      
    }
  }


}
