.projects {
    &--no-cursor {
        cursor: none;
    }

    &__container {
        height: 100vh;
    }
    &__item {
        position: relative;
        overflow: hidden;
        &-img {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            transform: scale(1.5);
            transition: 1000ms ease;
            transform-origin: 50% 100%;
            object-position: center;
            object-fit: cover;

            &--mob {
                display: none;
            }
        }

        &--active {
            img {
                transform: scale(1);
            }
        }

        &--hide {
            img {
                transform: scale(1.5);
            }
        }

        &--first {
            img {
                opacity: 1;
                transform: scale(1);
            }
        }

        &-overlay {
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
        }
    }

    &__info {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100%;
        color: #fff;
        z-index: 2;

        &-container {
            display: flex;
            align-items: flex-end;
            height: 100%;
            width: 100%;
            justify-content: flex-end;
            position: relative;
            overflow: hidden;

            &--active {
                overflow: visible;
            }
        }

        &-right {
            display: flex;
            align-items: flex-end;
            transition: 800ms ease all;
            transform: translateX(503px);
            justify-content: flex-end;
            position: relative;
        }
    }

    &__partners {
        // transform: translateX(60px);
        transition: 800ms ease all;
        margin-bottom: 50px;
        overflow: hidden;

        &-wrapper {
            transition: 1100ms ease all;
            position: relative;
            top: 0px;
        }

        &-slide {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: flex-end;
            padding: 10px 0;
        }
        &-title {
            text-align: right;
            margin-bottom: 50px;
            @include adaptiv-font(16, 9);

            &--lg {
                font-size: 24px;
                @include adaptiv-font(24, 16);

                font-weight: 800;
                margin-bottom: 0;
            }
        }

        &-container {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 32px;
        }

        &-link {
            font-size: inherit;
            line-height: inherit;
            display: inline-flex;
        }
    }

    &__menu-container {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 100vh;
        max-width: 503px;
        position: relative;
    }

    &__menu-btns {
        display: flex;
        position: absolute;
        z-index: 6;
        left: -32px;
        top: calc(50% - 30px);
    }

    &__menu-close {
        .projects__menu-icon {
            transform: rotate(180deg);
        }
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        opacity: 0;
    }

    &__menu-icon {
        circle,
        path {
            transition: 300ms ease all;
        }
    }

    &__menu-open {
        &:hover {
            .projects__menu-icon {
                circle {
                    fill: #fff;
                }
                path {
                    fill: #000;
                }
            }
        }
    }

    &__menu-close {
        transition: 500ms ease all;
        &:hover {
            .projects__menu-icon {
                circle {
                    fill: #fff;
                }
                path {
                    fill: #000;
                }
            }
        }
    }

    &__menu-group {
        display: flex;
        align-items: center;
    }

    &__menu-links {
        display: flex;
        align-items: flex-start;
        margin-top: 150px;
        flex-wrap: wrap;
        gap: 8px;
        max-width: 503px;
        padding-left: 60px;
        position: relative;
        z-index: 2;
        // transform: translateX(120px);
    }

    &__menu-link {
        border-radius: 24px;
        background: rgba(255, 255, 255, 0.1);
        @include adaptiv-font(16, 9);
        color: #fff;
        padding: 4px 8px;
        border: 1px solid transparent;
        transition: 300ms ease all;
        &:hover {
            border-color: #fff;
        }

        &--active {
            background: #fff;
            color: #242b2f;
        }
    }

    &__menu {
        // transform: translateX(60px);
        height: 70vh;
        overflow: hidden;
        width: 100%;
        padding-left: 60px;
        &-wrapper {
            height: 100%;
            overflow: auto;
            padding-bottom: 60px;
            scrollbar-width: none;
            &::-webkit-scrollbar {
                display: none;
            }

            &-items {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 8px;
                position: relative;
                transition: 900ms ease all;
            }
        }

        &-item {
            line-height: 110%;
            transition: 400ms ease all;
            opacity: 0.75;
            display: inline-flex;

            @include adaptiv-font(24, 16);

            span {
                display: inline-block;
                margin-right: 24px;
                min-width: 40px;
            }

            &--active {
                opacity: 1;
                @include adaptiv-font(40, 24);
            }
        }
    }

    &__info-right--active {
        transform: translateX(0);

        .projects__menu-open {
            opacity: 0;
            z-index: -1;
        }
        .projects__menu-close {
            opacity: 1;
            z-index: 4;
            // transform: translateX(-54px);
            background: rgba(36, 39, 54, 0.02);
            backdrop-filter: blur(8px);
            display: inline-block;
            border-radius: 50%;
        }

        .projects__menu-links {
            // transform: translateX(60px);
            // max-width: calc(496px + 60px);
        }

        .projects__menu,
        .projects__partners {
            // transform: translateX(0);
        }
        .projects__partners {
            transform: translateX(-100px);
        }

        .projects__menu-container {
            // background: rgba(36, 39, 54, 0.02);
            // backdrop-filter: blur(8px);
            position: relative;
            &::before {
                content: "";
                position: absolute;
                height: 100%;
                width: calc(100% + 100vw);
                top: 0;
                left: 0;
                background: rgba(36, 39, 54, 0.02);
                backdrop-filter: blur(8px);
            }
        }
    }

    &__button {
        width: 140px;
        height: 140px;
        background-color: #fff;
        border-radius: 50%;
        background-image: url(../img/projects/cursor.gif);
        background-position: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        font-weight: 700;
        position: absolute;
        z-index: 1;
    }

    &__pagination {
        display: flex;
        align-items: center;
        font-weight: 600;
        margin-bottom: 40px;
        @include adaptiv-font(40, 24);

        &-num {
            overflow: hidden;

            &-holder {
                position: relative;
                transition: 1100ms ease all;
                top: 0px;
            }

            &-item {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 5px 0;
            }
        }
        &-divider {
            min-width: 100px;
            width: 100px;
            height: 2px;
            background: #fff;
            margin: 0 8px;
        }
    }

    &__info-left {
        margin-bottom: 50px;
        position: absolute;
        left: 60px;
        bottom: 0;
    }

    &__main-info {
        overflow: hidden;

        &-wrapper {
            position: relative;
            top: 0px;
            transition: 1100ms ease all;
        }
        &-slide {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-end;
            padding: 15px 0;
        }
        &-desc {
            font-weight: 600;
            margin-bottom: 15px;
            @include adaptiv-font(24, 16);
        }
        &-title {
            font-weight: 800;
            line-height: 120%;
            margin-bottom: 32px;
            @include adaptiv-font(104, 40);
        }
        &-tags {
            display: flex;
            align-items: flex-start;
            gap: 12px;
            // padding-bottom: 10px;
            span {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                border-radius: 24px;
                border: 1px solid #fff;
                padding: 0 12px;
                @include adaptiv-font(16, 9);
            }
        }
    }
}

@media (max-width: 1440px) {
    .projects__info-left {
        left: 32px;
    }
}

@media (max-width: 1140px) {
    .projects__info-right {
        transform: translateX(449px);
        &--active {
            transform: translateX(0);
        }
    }
}
@media (max-width: $tablet) {
    .projects__info-container {
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
    }

    .projects__info-left {
        position: static;
        width: 100%;
        max-height: 100%;
    }

    .projects__info-right {
        align-items: flex-start;
        transform: none;
        position: static;
    }

    .projects__partners {
        transform: none;
    }

    .projects__menu-links {
        transform: none;
        margin-top: 130px;
        max-width: 100%;
        padding: 0 32px;
    }

    .projects__partners-slide {
        align-items: flex-start;
    }

    .projects__menu-container {
        height: 100vh;
        position: absolute;
        top: 0;
        max-width: 100%;
        left: 100%;
        width: 100%;
        background: rgba(36, 39, 54, 0.02);
        backdrop-filter: blur(8px);
        transition: 800ms ease all;
    }

    .projects__partners {
        margin-bottom: 25px;
    }

    .projects__info-right--active .projects__partners {
        transform: none;
    }

    .projects__info-right--active .projects__menu-container:before {
        content: none;
    }

    .projects__info-right--active .projects__menu-container {
        position: absolute;
    }

    .projects__menu-btns {
        left: -42px;
    }

    .projects__info-right--active {
        .projects__menu-container {
            left: 0;
        }
        .projects__menu-btns {
            left: calc(100% - 42px);
        }
    }

    .projects__partners-title {
        margin-bottom: 15px;
    }

    .projects__menu-close .projects__menu-icon circle {
        fill: #fff;
    }

    .projects__menu-close .projects__menu-icon path {
        fill: #000;
    }

    .projects__info-left {
        margin-bottom: 32px;
    }
    .projects__partners-container {
        justify-content: flex-start;
        // flex-wrap: wrap;
    }

    .projects__main-info-desc {
        margin-bottom: 8px;
    }

    .projects__main-info-title {
        margin-bottom: 24px;
    }
    .projects__menu {
        position: relative;
        left: -32px;
    }
    .projects__pagination {
        margin-bottom: 5px;
    }
}

@media (max-width: 760px) {
    .projects__item-img {
        display: none;
    }
    .projects__item-img--mob {
        display: block;
    }
    .projects__menu-links {
        margin-top: 90px;
        padding: 0 16px;
    }
}

.projects__test {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100vh;
    color: #fff;
    font-size: 20px;
    &-wrapper {
        max-width: 900px;
        margin: 250px auto 0;
        background: #000;
        padding: 15px;
    }
    .test-menu {
        margin: 5px;
        padding: 5px;
        background-color: green;
        height: 110px;
        overflow: auto;
        text-align: justify;
    }
}
