.project4 {

  //------Переиспользуемый заголовок-----//
  &.h2 {
    color: #536666;
    font-size: 40px;
    font-weight: 900;
    line-height: 120%;
    position: relative;
    padding-left: 40px;
    text-transform: uppercase;
    z-index: 1;

    &::after {
      content: '';
      position: absolute;
      left: -40px;
      top: 20px;
      background-image: url(../img/project4/decor-line.svg);
      background-repeat: no-repeat;
      width: 40px;
      height: 4px;
    }

    & span {
      margin-left: 22px;
      height: 2px;
      width: 100%;
      display: block;
      background-color: #eee;
    }

    &--line {
      &::before {
        content: '';
        position: absolute;
        left: 22px;
        top: 20px;
        background-color: #F3F3F5;
        width: 100%;
        height: 2px;
      }
    }

    @media (max-width: 1600px) {
      font-size: 36px;
    }

    @media (max-width: $desktop) {
      margin-left: -40px;
    }

    @media (max-width: $mobile) {
      font-size: 30px;
    }

    @media (max-width: $mobile-sm) {
      font-size: 20px;
      padding-left: 21px;
      margin-left: -21px;

      &::after {
        left: -16px;
        top: 10px;
        width: 20px;
        height: 2px;
      }
    }
  }

  //------Переиспользуемый label-----//
  &-label {
    font-weight: 900;
    font-size: 18px;
    text-transform: uppercase;
    line-height: 21px;
    background-color: #F2638A;
    color: #fff;
    padding: 10px 14px;
    position: relative;
    z-index: 1;

    &::after {
      content: '';
      position: absolute;
      left: -11px;
      top: -11px;
      border: 10px solid transparent;
      border-right: 10px solid #F3F3F5;
      rotate: 45deg;
    }

    @media (max-width: $mobile-sm) {
      padding: 7px 11px;
      font-size: 12px;
      line-height: 14px;
    }
  }

  &-sect {

    //---aboutUs------------//
    &__aboutUs {
      font-family: 'Raleway', 'Manrope', "sans-serif";
      padding: 200px 100px 150px 0px;
      display: flex;
      justify-content: space-between;
      gap: 120px;
      flex-wrap: wrap;
      position: relative;
      margin-left: 140px;

      & .project4-aboutUs {
        &__devices1 {
          position: absolute;
          right: 0;
          top: 0;
          max-width: 1071px;
          width: 89%;
          margin-right: 80px;
          margin-top: 15px;

          &-img {
            position: absolute;
            bottom: 0;
            z-index: 1;
            filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.04));

            & img {
              width: 100%;
            }

            &:nth-child(1) {
              max-width: 1071px;
            }

            &:nth-child(2) {
              max-width: 422px;
              left: -91px;
              bottom: -19px;
              margin-right: 68%;
            }

            &:nth-child(3) {
              max-width: 248px;
              left: 192px;
              bottom: -41px;
              margin-right: 53%;
            }
          }
        }

        &__block {
          max-width: 560px;
          margin-left: 40px;

          & p {
            font-weight: 500;
            font-size: 24px;
            line-height: 160%;
            color: #536666;
            margin-bottom: 40px;
            position: relative;

            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: -120px;
              background-image: url(../img/project4/quotes.svg);
              width: 80px;
              height: 50px;
              background-repeat: no-repeat;
              background-size: 80px;
            }
          }

          & ul {
            list-style: none;
            padding: 0;
            display: flex;
            gap: 10px;

            & li {
              display: flex;

              & a {
                color: #fff;
                font-weight: 500;
                font-size: 16px;
                line-height: 160%;
                opacity: .5;
                background-color: #03afa7;
                border-radius: 10px;
                padding: 6px 16px;

                @media (hover: hover) {
                  &:hover {
                    opacity: 1;
                  }

                }
              }
            }
          }
        }

      }
    }

    //---analytics------------//
    &__analytics {
      position: relative;
      padding: 144px 60px 0px 100px;
      background-color: #F3F3F5;

      & .project4-analytics {
        &__pluses {
          position: absolute;
          left: -530px;
          top: 48%;
          width: 834px;
          height: 800px;
          z-index: -1;
        }

        &__block {
          padding-left: 40px;
          margin: 56px 0px 80px;
          display: flex;
          gap: 30px;
          justify-content: space-between;
        }

        &__text {
          font-weight: 500;
          font-size: 24px;
          line-height: 160%;
          max-width: 600px;
        }

        &__mindmap {
          display: flex;
          flex-direction: row;

          & .mindmap {
            &__wrap {
              & div img {
                display: block !important;
              }
            }

            &__items-m {
              display: none;
            }

            &__img {
              position: absolute;
              right: 0;
              max-width: 100%;
              transform: translateY(0) !important;

              &--t {
                display: none;
              }
            }

            &__line {
              width: 100%;

              & img:last-child {
                display: none;
              }
            }

            &__items {
              display: flex;
              flex-direction: column;
              row-gap: 28px;

              & div {
                height: 43px;
                display: flex;
                position: relative;

                & img {
                  max-width: 100%;
                  position: absolute;
                  right: 0;
                  top: 50%;
                  transform: translateY(-50%);
                }
              }

              & span {
                font-weight: 700;
                font-size: 16px;
                line-height: 120%;
                color: #fff;
                padding: 0px 16px;
                text-align: center;
                border-radius: 4px;
                display: flex;
                align-items: center;
                z-index: 1;
              }

              &:nth-child(1) {
                flex-basis: 28%;
                position: relative;

                &::after {
                  content: '';
                  position: absolute;
                  top: 21.5px;
                  right: 0;
                  max-width: 255px;
                  width: 100%;
                  height: 100%;
                  background-image: url(../img/project4/mindmap-pc/left.svg);
                  background-repeat: no-repeat;
                }

                & div {
                  z-index: 1;
                  margin-top: 280px;
                }

                & span {
                  background-color: #D53863;

                }
              }

              &:nth-child(2) {
                position: relative;
                flex-basis: 27.69%;

                & span {
                  background-color: #00AAA0;
                  position: relative;
                  white-space: nowrap;

                  & br {
                    display: none;
                  }
                }

              }

              &:nth-child(3) {

                & span {
                  color: #536666;
                  background-color: #fff;
                }
              }
            }
          }
        }
      }

    }

    //---prototypes------------//
    &__prototypes {
      padding: 180px 100px 0px 115px;
      background-color: #fff;
      position: relative;

      & .project4-label::after {
        border-right-color: #fff;
      }

      &::after {
        content: '';
        content: "";
        position: absolute;
        top: 0px;
        left: 0;
        width: 100%;
        height: 578px;
        border-top: 578px solid #F3F3F5;
        border-left: 1460px solid transparent;
      }

      & .project4.h2 {
        margin-left: 40px;
        margin-top: 56px;
        margin-bottom: 32px;
      }

      & .project4-prototypes {
        &__text {
          font-weight: 500;
          font-size: 24px;
          line-height: 160%;
          max-width: 720px;
          z-index: 1;
          position: relative;
        }
      }
    }

    //---typography------------//
    &__typography {
      padding: 187px 60px 100px 100px;

      & .project4.h2 {
        margin: 62px 0px 100px 40px;
        display: flex;
        align-items: center;

      }

      & .project4-label::after {
        border-right-color: #fff;
      }

      & h3 {
        font-weight: 900;
        font-size: 96px;
        line-height: 120%;
        color: #425351;
        margin-bottom: 40px;
        text-transform: uppercase;
      }

      & h4 {
        font-weight: 500;
        font-size: 48px;
        line-height: 120%;
        color: #425351;
        margin-bottom: 40px;
      }

      & p {
        font-weight: 700;
        font-size: 36px;
        line-height: 160%;
        margin-bottom: 80px;
        color: #A9B8B8;

        &:last-child {
          font-weight: 500;
          margin-bottom: 0px;
        }
      }

    }

    //---colors------------//
    &__colors {
      padding: 100px 50px 100px 100px;

      & .project4.h2 {
        margin-bottom: 100px;
        display: flex;
        align-items: center;
        margin-left: 40px;
      }

      & ul {
        list-style: none;
        padding: 0;
        display: flex;
        column-gap: 50px;
        flex-wrap: wrap;
        row-gap: 60px;

        & li {
          width: 220px;
          height: 140px;
          border-right: 1px solid;
          border-bottom: 1px solid;
          display: flex;
          justify-content: flex-end;
          align-items: flex-end;
          position: relative;
          border-color: #d53863;

          &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 140px;
            border-top: 140px solid #d53863;
            border-right: 220px solid transparent;
          }

          &:nth-child(2) {
            border-color: #00AAA0;

            &::after {
              border-top-color: #00AAA0;
            }
          }

          &:nth-child(3) {
            border-color: #536666;

            &::after {
              border-top-color: #536666;
            }
          }

          &:nth-child(4) {
            border-color: #A9B8B8;

            &::after {
              border-top-color: #A9B8B8;
            }
          }

          &:nth-child(5) {
            border-color: #F3F3F5;

            &::after {
              border-top-color: #F3F3F5;
            }
          }

          & p {
            font-weight: 700;
            padding: 0px 20px 20px 0px;
            font-size: 20px;
            line-height: 120%;
            font-feature-settings: 'pnum'on, 'lnum'on;
          }
        }

      }
    }

    //---elements------------//
    &__elements {
      padding: 100px 0px 100px 45px;

      & .project4.h2 {
        margin-bottom: 76px;
        margin-left: 95px;
        display: flex;
        align-items: center;
      }

      & .elements__img {
        & img {
          max-width: 100%;
          height: auto;
        }
      }
    }

    //---design------------//
    &__design {
      padding: 81px 60px 100px 100px;
      position: relative;

      & .project4-label::after {
        border-right-color: #fff;
      }

      & .project4-design {

        &__pluses {
          position: absolute;
          top: 366px;
          left: -54px;
          z-index: -1;
        }

        &__block {
          padding: 56px 0px 100px 40px;
          display: flex;
          justify-content: space-between;
          gap: 33px;

          & p {
            max-width: 600px;
            font-weight: 500;
            font-size: 24px;
            line-height: 160%;
          }
        }

        &__img {
          z-index: 2;
          position: relative;
          padding: 0 0 150.5%;

          & img {
            filter: drop-shadow(0px 24.375px 32.5px rgba(56, 56, 56, 0.15));
            max-width: 100%;
            height: auto;
            position: absolute;
          }
        }

        &__hover1 {
          margin-right: 55%;

          & img {
            position: relative;
            margin-left: 64%;
            margin-top: 179%;
            width: 51%;
            filter: drop-shadow(13px 39.8125px 19.5px rgba(34, 101, 101, 0.06)) drop-shadow(13px 37.375px 39px rgba(34, 101, 101, 0.2));
          }
        }

        &__hover2 {
          margin-right: 64%;

          & img {
            position: relative;
            margin-left: 15%;
            margin-top: 67%;
            width: 70%;
            filter: drop-shadow(13px 39.8125px 19.5px rgba(34, 101, 101, 0.06)) drop-shadow(13px 37.375px 39px rgba(34, 101, 101, 0.2));
          }
        }
      }
    }

    //---desktop------------//
    &__desktop {
      margin-top: -854px;
      padding-top: 940px;
      padding-left: 100px;
      padding-bottom: 113px;
      background-color: #00AAA0;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: -3px;
        left: 0;
        width: 100%;
        height: 578px;
        border-bottom: 578px solid #fff;
        border-left: 1460px solid transparent;
      }

      & .project4-desktop {
        &__pluses1 {
          position: absolute;
          top: -337px;
          left: -29px;
        }

        &__pluses2 {
          position: absolute;
          bottom: 410px;
          left: -36px;
        }

        &__text {
          font-weight: 500;
          font-size: 24px;
          line-height: 160%;
          max-width: 720px;
          margin-bottom: 129px;
          color: #fff;
        }

        &__block {
          display: flex;
          gap: 44px;
          z-index: 1;
          position: relative;
        }

        &__left {
          display: flex;
          flex-direction: column;
          gap: 44px;
          padding-top: 70px;
          position: relative;

          &::after {
            content: "DESKTOP";
            top: -31px;
            left: -59px;
            font-weight: 900;
            position: absolute;
            font-size: 128px;
            line-height: 120%;
            color: #fff;
            opacity: .3;
          }

          & img {
            max-width: 100%;
            height: auto;
            z-index: 1;
            filter: drop-shadow(-11.8471px 17.7707px 5.92356px rgba(34, 101, 101, 0.06)) drop-shadow(-11.8471px 11.8471px 35.5414px rgba(34, 101, 101, 0.2));
          }
        }

        &__right {
          display: flex;
          flex-direction: column;
          gap: 44px;

          & img {
            max-width: 100%;
            height: auto;
            filter: drop-shadow(-11.8471px 17.7707px 5.92356px rgba(34, 101, 101, 0.06)) drop-shadow(-11.8471px 11.8471px 35.5414px rgba(34, 101, 101, 0.2));
          }
        }
      }
    }

    //---ICONS------------//
    &__icons {
      padding: 90px 0px 0px;

      & .project4-icons {
        &__grid {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(222px, 1fr));
        }

        &__item {
          min-height: 243px;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #F3F3F5;
          border-top: none;

          &:first-child {
            grid-column: span 2;
            padding-left: 120px;
            flex-wrap: wrap;
            align-items: flex-start;
            justify-content: flex-start;

            & h2 {
              margin-left: -40px;
            }

            & p {
              font-weight: 500;
              font-size: 24px;
              line-height: 160%;
            }
          }
        }

      }
    }

    //---TABLET------------//
    &__tablet {
      padding: 200px 80px 100px 100px;
      background-color: #FAFAFC;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        bottom: -3px;
        left: 0;
        width: 100%;
        height: 578px;
        border-bottom: 578px solid #F3F3F5;
        border-right: 1460px solid transparent;
      }

      & .project4-tablet {
        &__pluses {
          position: absolute;
          left: 15%;
          bottom: 5%;
          max-width: 100%;
          height: auto;
          z-index: 1;
        }

        &__text {
          display: flex;
          justify-content: space-between;
          gap: 30px;
          padding-left: 40px;
          margin-bottom: 100px;

          & p {
            font-weight: 500;
            font-size: 24px;
            line-height: 160%;
            max-width: 600px;
          }
        }

        &__block {
          display: flex;
          gap: 44px;
          position: relative;
          z-index: 1;
        }

        &__left {
          display: flex;
          flex-direction: column;
          gap: 44px;
          flex-basis: 33.33%;
          position: relative;
          margin-top: 266px;

          &::after {
            content: "TABLET";
            top: -110px;
            left: -52px;
            font-weight: 900;
            position: absolute;
            font-size: 128px;
            line-height: 120%;
            color: #536666;
            opacity: .1;
          }

          & img {
            max-width: 100%;
            z-index: 1;
            height: auto;
            filter: drop-shadow(-29.5287px 17.7172px 29.5287px rgba(0, 0, 0, 0.08));
          }
        }

        &__center {
          display: flex;
          flex-direction: column;
          gap: 44px;
          flex-basis: 33.33%;
          position: relative;

          & img {
            max-width: 100%;
            height: auto;

            filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.04));

            &:first-child {
              position: absolute;
              top: 0;
              left: -25px;
              max-width: calc(100% + 52px);
              height: auto;
              box-shadow: 0px 20px 25px -5px rgba(0, 0, 0, 0.1);
              filter: drop-shadow(0px 7.38217px 7.38217px rgba(0, 0, 0, 0.04));
              z-index: 1;
              border-radius: 30px;
            }
          }
        }

        &__right {
          display: flex;
          flex-direction: column;
          gap: 44px;
          flex-basis: 33.33%;
          margin-top: 120px;

          & img {
            filter: drop-shadow(-29.5287px 17.7172px 29.5287px rgba(0, 0, 0, 0.08));
            max-width: 100%;
            height: auto;
          }
        }
      }
    }

    //---MOBILE------------//
    &__mobile {
      padding: 80px 0px 0px 100px;
      background-color: #F3F3F5;
      position: relative;
      overflow: hidden;

      &:before {
        content: "";
        width: 100%;
        position: absolute;
        z-index: 2;
        bottom: 0px;
        left: 0;
        height: 310px;
        background: linear-gradient(180deg, #F3F3F5 16.02%, rgba(243, 243, 245, 0) 100%);
        transform: matrix(1, 0, 0, -1, 0, 0);
      }

      & .project4-mobile {
        margin-bottom: -35.5%;

        &__pluses1 {
          position: absolute;
          left: 21%;
          top: 22.8%;
          max-width: 100%;
          height: auto;
        }

        &__pluses2 {
          position: absolute;
          right: -41%;
          top: 46%;
          max-width: 100%;
          height: auto;
        }

        &__text {
          font-weight: 500;
          font-size: 24px;
          line-height: 160%;
          max-width: 720px;
          margin-bottom: 80px;
        }

        &__block {
          display: flex;
          gap: 74px;
        }

        &__items {
          display: flex;
          flex-direction: column;
          gap: 35px;

          &:nth-child(1) {
            margin-top: 137px;
            position: relative;

            &::after {
              content: "MOBILE";
              top: -118px;
              left: -48px;
              font-weight: 900;
              position: absolute;
              font-size: 128px;
              line-height: 120%;
              color: #536666;
              opacity: .1;
            }
          }

          &:nth-child(2) {
            margin-top: 100px;
          }

          &:nth-child(4) {
            margin-top: 75px;
          }
        }

        &__item {
          z-index: 1;

          & img {
            max-width: 100%;
            height: auto;
            filter: drop-shadow(-33.6134px 20.1681px 33.6134px rgba(0, 0, 0, 0.08));
            border-radius: 10px;
          }
        }
      }
    }
  }

  & .project4-prototypes__img {
    position: relative;
    margin-top: -384px;

    & img {
      max-width: 100%;
      height: auto;
    }

    &::before {
      content: "";
      width: 100%;
      position: absolute;
      z-index: 2;
      bottom: 0;
      left: 0;
      height: 415px;
      background: linear-gradient(180deg, #FFFFFF 16.02%, rgba(255, 255, 255, 0) 100%);
      transform: matrix(1, 0, 0, -1, 0, 0);
    }
  }

  .project {
    &-hero {
      background-color: rgb(25, 178, 169);
      background-image: url(../img/project4/hero/heart1.svg);
      background-repeat: no-repeat;
      height: 1210px;
      background-size: 1462px 1380px;
      background-position: -510px 150px;

      &__block {
        display: flex;
        padding-top: 322px;
        padding-left: 100px;
      }

      &__left {
        flex-basis: 27.8%;
        flex-grow: 0;
      }

      &__logo {
        max-width: 416px;
        margin-bottom: 67px;
      }

      &__text {
        font-size: 24px;
        font-weight: 500;
        opacity: .8;
        line-height: 160%;
        font-family: 'Raleway', 'Manrope', "sans-serif";
      }
    }

    &-main {
      margin-top: 1210px;

      &__wp {
        display: grid;
        grid-template-columns: 460px minmax(320px, 1460px);
        grid-template-rows: auto auto;
        position: relative;
      }

      &__left {
        grid-row: 1/3;
        width: auto;

        &-icons {
          width: 320px;
        }
      }

      &__right {
        font-family: 'Raleway', 'Manrope', "sans-serif";
        color: #536666;
        width: 100%;
        padding-top: 0;
      }

      &__left-link {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 13px;
      }

      &__sect:nth-child(even) {
        padding: 120px 0px;
        background-image: url(../img/project/line-back.png);
        background-position: 100% 0;
        background-size: 100% auto;

        & .left {
          height: 174px;
        }

        & .right a {
          display: flex;
          align-items: center;
          font-style: normal;
          font-weight: 700;
          font-size: 16px;
          line-height: 140%;
          color: #B30B00;
          margin-top: 40px;
          transition: .25s;

          & svg {
            margin-right: 16px;
          }
        }
      }

      &__sect:nth-child(odd) {
        padding: 50px 0px 117px;

        & .left {
          margin-top: -20px;
          height: 278px;
        }

        & .right .main-hero__bottom-left {
          margin-top: 57px;

          & span {
            font-weight: 600;
            font-size: 16px;
            line-height: 140%;
          }
        }

        & .main-hero__bottom-right {
          margin-top: 75px;

          & a {
