              gap: 8px;

              & li a {
                font-size: 12px;
              }
            }
          }
        }
      }

      &__analytics {
        padding: 51px 19px 0px 13px;

        & .project4-analytics {
          &__block {
            row-gap: 13px;
            padding-left: 0;
            margin: 28px 0px 29px;

            & .project4.h2 {
              margin-left: 15px;
            }
          }

          &__text {
            font-size: 16px;
          }

          &__mindmap {
            margin-right: 0;
          }
        }
      }

      &__prototypes {
        padding: 36px 19px 0px 13px;

        & .project4.h2 {
          margin: 28px 0px 13px 16px;
        }

        & .project4-prototypes {
          &__text {
            margin-left: 0;
          }
        }
      }

      &__typography {
        padding: 98px 10px 40px 16px;

        & .project4.h2 {
          margin: 28px 0px 28px 16px;
        }

        & h3 {
          font-size: 36px;
          margin-bottom: 12px;
        }

        & h4 {
          font-size: 32px;
          margin-bottom: 10px;
        }

        & p {
          font-size: 14px;
          margin-bottom: 27px;
          max-width: 215px;
        }
      }

      &__colors {
        padding: 8px 0px 30px 17px;

        & .project4.h2 {
          margin-left: 15px;
          margin-bottom: 27px;
        }

        & ul {
          column-gap: 16px;
          row-gap: 28px;
        }

        & ul li {
          height: 75px;
          width: 136px;

          &::after {
            height: 75px;
            border-top-width: 75px;
            border-right-width: 136px;
          }

          & p {
            font-size: 12px;
            padding: 0px 12px 12px 0px;
          }
        }
      }

      &__elements {
        padding: 20px 0px 25px 10px;

        & .project4.h2 {
          margin-bottom: 22px;
          margin-left: 20px;
        }
      }

      &__design {
        padding: 30px 16px 0px 10px;

        & .project4-design {
          &__pluses {
            display: none;
          }

          &__block {
            padding: 27px 0px 28px 6px;
            row-gap: 13px;

            & .project4.h2 {
              margin-left: 15px;
            }
          }
        }
      }

      &__desktop {
        margin-top: -188px;
        padding-top: 210px;
        padding: 210px 16px 50px;
        padding-bottom: 30px;

        &::after {
          bottom: -1px;
          height: 126px;
          border-bottom-width: 126px;
          border-left-width: 320px;
        }

        & .project4-desktop {
          &__block {
            gap: 10px;
          }

          &__left,
          &__right {
            gap: 10px;
          }

          &__left {
            padding-top: 15px;

            &::after {
              font-size: 30px;
              top: -10px;
            }
          }

          &__pluses1 {
            width: 173px;
            height: 166px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }

          &__pluses2 {
            width: 173px;
            height: 166px;
            bottom: 150px;
            left: 50%;
            transform: translate(-50%, 0%);
          }

          &__text {
            font-size: 14px;
            margin-left: 0px;
            margin-bottom: 36px;
          }
        }
      }

      &__icons {
        padding-top: 17px;

        & .project4-icons {
          &__grid {
            display: flex;
            flex-wrap: wrap;
          }

          &__item {
            min-height: 80px;
            flex-basis: 25%;
            flex-grow: 1;

            & svg {
              width: 42px;
              height: 42px;
            }

            &:first-child {
              flex-basis: 100%;
              padding: 0px 5px 20px 16px;
              justify-content: flex-start;
              flex-wrap: wrap;
              gap: 16px;

              & h2 {
                margin-left: 13px;
              }

              & p {
                font-size: 14px;
              }
            }
          }
        }
      }

      &__tablet {
        padding: 112px 16px 53px;

        &::after {
          height: 127px;
          border-bottom-width: 127px;
          border-right-width: 330px;
        }

        & .project4-tablet {
          &__pluses {
            width: 141px;
            height: 135px;
            bottom: 3%;
          }

          &__text {
            gap: 14px;
            margin-bottom: 35px;
            padding-left: 0;

            & h2 {
              margin-left: 12px;
            }

            & p {
              font-size: 14px;
            }
          }

          &__left {
            margin-top: 48px;
          }

          &__right {
            margin-top: 15px;
          }

          &__center {
            & img:first-child {
              border-radius: 5px;
            }
          }
        }
      }

      &__mobile {
        padding: 25px 16px 0px;

        & .project4-mobile {

          &__pluses1,
          &__pluses2 {
            width: 187px;
            height: 180px;
          }

          &__pluses1 {
            top: 32.8;
          }

          &__text {
            font-size: 14px;
            padding-left: 0px;
            margin-bottom: 35px;
          }

          &__items {
            &:first-child {
              margin-top: 18px;

              &::after {
                font-size: 30px;
                top: -34px;
                left: -6px;
              }
            }

            &:nth-child(2) {
              margin-top: 10px;
            }

            &:nth-child(4) {
              margin-top: 18px;
            }
          }

          &__item {
            & img {
              border-radius: 5px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 360px) {
  .project4 {
    & .project {
      &-hero {
        height: 509px;
      }

      &-main {
        margin-top: 509px;
      }
    }
  }
}