.jobs {
  margin: 120px 0 200px;

  &-title {
    color: $text-color;
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    line-height: 120%;
    margin-bottom: 64px;
  }

  &-elem {
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-between;
    margin-top: 24px;
    padding-bottom: 24px;
    border-bottom: 2px solid $text-color;
  }
}

.job {
  &-title-and-time {
    width: 55%;
    position: relative;
  }

  &-title {
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    line-height: 140%;
  }

  &-time {
    text-align: center;
    position: absolute;
    right: 0px;
    bottom: 1px;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 180%;
  }

  &-arrow {
    position: absolute;
    bottom: 0;
    right: 220px;
  }
}

@media (max-width: 1400px) {
  .job {
    &-title-and-time {
      width: 65%;
      // display: flex;
      // flex-wrap: wrap;
      // gap: 16px;
      // justify-content: space-between;
    }

    &-time {
      text-align: right;
      // display: inline-flex;
      // align-items: center;
      // position: inherit;
    }

    &-arrow {
      display: none;
    }
  }
}

@media (max-width: $tablet) {
  .jobs {
    margin-top: 80px;
    margin-bottom: 136px;

    &-title {
      font-size: 40px;
      margin-bottom: 37px;
    }

    &-elem {
      margin-top: 20px;
      padding-bottom: 16px;
    }
  }

  .job {
    &-title-and-time {
      width: 70%;
    }

    &-title {
      font-size: 24px;
    }

    &-time {
      font-size: 16px;
      left: calc(50% - 16px);
    }
  }
}

@media (max-width: $mobile) {
  .jobs {
    margin-top: 45px;
    margin-bottom: 78px;

    &-title {
      font-size: 24px;
      margin-bottom: 26px;
    }

    &-elem {
      margin-top: 16px;
      padding-bottom: 7px;
    }
  }

  .job {
    &-title-and-time {
      margin-bottom: 0px;
      display: flex;
      flex-direction: column;
    }

    &-title {
      font-size: 16px;
    }

    &-time {
      text-align: left;
      position: static;
      font-size: 12px;
    }
  }
}