[data-whatintent="touch"],
[data-whatintent="mouse"] {
    *:focus {
        outline: none;
    }
}

* {
    box-sizing: border-box;
    outline: none;
    //	@extend %scrollbar;
}

*::before,
*::after {
    box-sizing: inherit;
}

html {
    //touch-action: manipulation;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    font-family: $default-font;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 180%;
    color: $text-color;
    overflow-x: hidden;
    height: 100% !important;
    cursor: default;

    @media (max-width: 1600px) {
        font-size: 20px;
    }

    @media (max-width: 990px) {
        font-size: 16px;
        line-height: 182%;
    }

    @media (max-width: 760px) {
        line-height: 184%;
        font-size: 12px;
    }

    @media (max-width: 480px) {
        overflow-x: hidden;
        max-width: 100vw;
    }
}

main {
    overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

ul,
ol {
    margin: 0;

    &.no-style {
        padding: 0;
        list-style: none;
    }
}

button {
    border: none;
    cursor: pointer;
    font-family: $default-font;
}

img {
    border: none;
}

a {
    text-decoration: none;
    color: inherit;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    transition: opacity 0.25s;

    // @media (min-width: 991px) {
    // 	&:hover {
    // 		opacity: 0.8;
    // 	}
    // }
}

[contenteditable="true"]:empty:before {
    content: attr(data-placeholder);
    color: #131313;
    font-size: inherit;
    display: block;
    /* For Firefox */
}

select {
    appearance: none;

    &::-ms-expand {
        // для IE
        display: none;
    }
}

input[type="number"] {
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

b {
    font-weight: 700;
}

blockquote {
    margin: 0;
    padding-left: 22px;
    border-left: 2px solid $text-color;
    font-style: italic;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-right: 19px;

    p {
        font-style: italic;
        font-weight: normal;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 11px;

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    @media (max-width: 1140px) {
        padding-right: 0;
        padding-left: 18px;
    }

    @media (max-width: 990px) {
