            font-size: 14px;
            line-height: 20px;
        }

        font-size: 14px;
        line-height: 20px;
    }

    @media (max-width: 760px) {
        p {
            font-size: 12px;
            line-height: 16px;
            margin-bottom: 9px;
        }

        font-size: 12px;
        line-height: 16px;
        padding-top: 1px;
        padding-left: 15px;
        padding-bottom: 0;
    }
}

