// Кнопки
.btn {
    position: relative;
    height: 42px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    display: inline-flex;
    align-items: center;
    padding-bottom: 2px;
    padding-right: 13px;
    padding-left: 13px;
    border: 2px solid $text-color;
    border-top: 1px solid $text-color;
    border-left: none;
    margin-top: 8px;
    margin-left: 8px;
    transition: background 0.25s, color 0.25s, border-color 0.25s;
    z-index: 1;
    border-radius: 1px 1px 0px 2px;

    &::before {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 43px;
        top: -6px;
        left: -12px;
        transform: skewY(41deg) rotateY(37deg);
        border-top: 3px solid $text-color;
        border-bottom: 3px solid $text-color;
        border-left: 2px solid $text-color;
        border-right: 3px solid $text-color;
        background: transparent;
        border-radius: 1px 0 2px 0;
        transition: inherit;
    }

    &::after {
        content: "";
        position: absolute;
        display: block;
        border-top: 2px solid $text-color;
        border-bottom: 2px solid $text-color;
        border-right: 3px solid $text-color;
        width: calc(100% + 2px);
        height: 11px;
        top: -11px;
        left: -5px;
        transform: skewX(50deg) rotateY(0deg);
        border-left: none;
        border-radius: 2px 0 3px 0;
        transition: inherit;
        background: transparent;
    }

    &:hover {
        background: $text-color;
        color: $wcol;
        border-color: $wcol;

        &::before,
        &::after {
            background: $text-color;
            border-color: $wcol;
        }
    }

    // &[disabled] {
    // 	background: $cg;
    // 	color: $cg2;
    // 	cursor: not-allowed;
    // }

    // &:active {
    // 	background: #FF6E5A;
    // }

    @media (max-width: 575px) {
        height: 35px;
        padding-left: 15px;
        padding-right: 13px;
        border-width: 1px;
        margin-top: 5px;
        margin-left: 6px;
        font-weight: bold;
        font-size: 12px;
        line-height: 140%;
        padding-bottom: 1px;

        &::before {
            width: 10px;
            height: 35px;
            top: -4px;
            border-width: 1px;
            left: -8px;
            border-radius: 2px 0 2px 0;
        }

        &::after {
            height: 7px;
            top: -7px;
            border-width: 1px;
            left: -4px;
            // width: calc(100% + 1px);
        }
    }
}

