// Ссылки

.a1 {
    color: $cg2;
    transition: 0.25s;
    padding-bottom: 4px;
    border-bottom: 1px solid transparent;

    &:hover {
        border-color: $cr;
        color: $text-color;
    }

    &[disabled] {
