    }
}

.a2 {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    color: $cb;
    padding-bottom: 2px;
    border-bottom: 1px solid transparent;
    display: inline;
    transition: 0.25s;

    &:hover {
