    }
}

.link {
    display: block;
    font-family: $default-font;
    font-weight: 400;
    font-size: 10px;
    line-height: 190%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #ffffff;
    opacity: 0.8;
    transition: 0.3s;
    transition-property: opacity;

    &:hover {
        opacity: 1;
    }

    &:active {
        opacity: 0.8;
    }

    &[disabled] {
        opacity: 0.2;
    }
}

.link-arrow {
    position: relative;
    padding-right: 27px;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;

    span {
        width: 16px;
        height: 16px;
        overflow: hidden;
        position: absolute;
        top: 3px;
        left: calc(100% - 16px);
        display: inline-block;
        transition: 0.25s;

        svg {
            position: absolute;
            right: 0;
            top: 0;
            transition: 0.25s;
        }
    }

    &:hover {
        span {
            width: 30px;

            svg {
                fill: $cr;
            }
        }
    }

    @media (max-width: 575px) {
        div {
            display: none;
        }

        span {
            top: -9px;
        }
    }
}

.link-drop {
    display: flex;
    font-family: $default-font;
    font-weight: 400;
    font-size: 10px;
    line-height: 190%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #ffffff;
    opacity: 1;
    transition: 0.3s;
    transition-property: opacity;
    position: relative;
    white-space: nowrap;

    &:hover {
        opacity: 0.7;
    }

    &:active {
        opacity: 1;
    }

    &[disabled] {
        opacity: 0.1;
    }

    &:after {
        content: url(../img/svg/link-drop.svg);
        margin-left: 11px;
    }
}

