// Чекбокс
.checkbox {
    &-old {
        display: none;
    }

    &-custom {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        width: 20px;
        height: 20px;
        border-radius: 0px;
        background: rgba(255, 255, 255, 0.06);
        border: 1px solid rgba(255, 255, 255, 0.6);
        transition: 0.3s;
        cursor: pointer;
    }

    &-label {
        display: inline-block;
        vertical-align: middle;
        color: rgba(255, 255, 255, 0.3);
        padding-left: 8px;
        transition: 0.3s;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
    }

    &-old:checked + &-custom {
        background: rgba(255, 255, 255, 1);
        transition: 0.3s;
    }

    &-old:checked + &-custom::before {
        content: "";
        display: block;
        position: absolute;
        left: 4px;
        top: 5px;
        width: 10px;
        height: 5px;
        border-bottom: 2px solid black;
        border-left: 2px solid black;
        transform: rotate(-50deg) skew(-5deg);
        border-radius: 2px;
        transition: 0.3s;
    }

    &-old:checked + &-custom + &-label {
        color: rgba(255, 255, 255, 0.8);
    }

    &-old[disabled] + &-custom {
        background: rgba(255, 255, 255, 0.012) !important;
        border: 1px solid rgba(255, 255, 255, 0.12) !important;
        transition: 0.3s;
        cursor: default;
    }

    &-old[disabled] + &-custom::before {
        display: none !important;
    }

    &-old[disabled] + &-custom + &-label {
        color: rgba(255, 255, 255, 0.06) !important;
        cursor: default;
    }

    &-old:checked[disabled] + &-custom {
        background: rgba(255, 255, 255, 0.2) !important;
        border: 1px solid rgba(255, 255, 255, 0) !important;
        transition: 0.3s;
    }

    &-old:checked[disabled] + &-custom::before {
        content: "";
        display: block !important;
        position: absolute;
        left: 4px;
        top: 5px;
        width: 10px;
        height: 5px;
        border-bottom: 2px solid rgba(0, 0, 0, 0.2) !important;
        border-left: 2px solid rgba(0, 0, 0, 0.2) !important;
        transform: rotate(-50deg) skew(-5deg);
        border-radius: 2px;
        transition: 0.3s;
    }

    &-old:checked[disabled] + &-custom + &-label {
        color: rgba(255, 255, 255, 0.16) !important;
    }

    &:hover &-custom {
        background: rgba(255, 255, 255, 0.1);
        border: 1px solid #ffffff;
    }

    &:hover &-label {
        color: rgba(255, 255, 255, 0.8);
    }

    &:hover &-old:checked + &-custom {
        background: #ffffff;
    }

    &:hover &-old:checked + &-custom::before {
        border-color: rgba(0, 0, 0, 0.3);
    }

    &.wrong {
        .checkbox-custom {
            border-color: red;
        }
    }

    @media (max-width: 560px) {
        &-custom {
            height: 16px;
            width: 16px;
        }

        &-label {
            font-size: 10px;
            line-height: 15px;
            padding-left: 13px;
        }
    }
}
