.service {
    .works-hero {
        padding-top: 103px;

        @media (max-width: 990px) {
            padding-top: 176px;
            padding-bottom: 47px;

            &__wp {
                h2 {
                    font-size: 40px;
                    line-height: 140%;
                    margin-top: 0px;
                }
            }
        }

        @media (max-width: 760px) {
            padding-top: 84px;
            padding-bottom: 27px;

            &__wp {
                h2 {
                    font-size: 24px;
                    line-height: 120%;
                    margin-top: 0px;
                }
            }
        }
    }

    &-main {
        position: relative;

        &__el {
            position: relative;
            width: 100%;

            &-projecting {
                margin-top: 70px;

                &.service-main__el .service-main__el-img {
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    overflow: hidden;
                    line-height: 0px;
                    z-index: 1;
                    height: 100%;

                    &-main {
                        height: 100%;
                    }

                    &-second {
                        position: absolute;
                        top: -130px;
                        left: 159px;
                        mix-blend-mode: color;
                        height: calc(100% + 136px);
                    }
                }

                &.service-main__el .container {
                    justify-content: flex-end;
                    padding-top: 108px;
                    min-height: 935px;
                    height: auto;
                }
            }

            &-design {
                margin-top: 30px;

                &.service-main__el .service-main__el-img {
                    position: absolute;
                    right: 0px;
                    top: -70px;
                    overflow: hidden;
                    margin-top: 70px;
                    line-height: 0px;
                    z-index: 1;
                    height: 100%;

                    &-main {
                        height: 100%;
                    }

                    &-second {
                        position: absolute;
                        top: -41px;
                        right: 161px;
                        mix-blend-mode: color;
                        height: calc(100% + 205px);
                    }
                }

                &.service-main__el .container {
                    justify-content: flex-start;
                    align-items: center;
                    padding-top: 13px;
                    min-height: 863px;
                    height: auto;
                }
            }

            &-layout {
                margin-top: 44px;

                &.service-main__el .service-main__el-img {
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    overflow: hidden;
                    margin-top: 0px;
                    line-height: 0px;
                    z-index: 1;
                    height: 100%;

                    &-main {
                        height: 100%;
                    }

                    &-second {
                        position: absolute;
                        top: -17px;
                        left: 160px;
                        mix-blend-mode: color;
                        height: calc(100% + 109px);
                    }
                }

                &.service-main__el .container {
                    justify-content: flex-end;
                    align-items: center;
                    padding-top: 100px;
                    min-height: 908px;
                    height: auto;
                }
            }

            &-seo {
                margin-top: 66px;

                &.service-main__el .service-main__el-img {
                    position: absolute;
                    right: 0px;
                    top: -70px;
                    overflow: hidden;
                    margin-top: 70px;
                    line-height: 0px;
                    z-index: 1;
                    height: 100%;

                    &-main {
                        height: 100%;
                    }

                    &-second {
                        position: absolute;
                        top: 0px;
                        right: 159px;
                        mix-blend-mode: color;
                        height: calc(100% + 143px);
                    }
                }

                &.service-main__el .container {
                    justify-content: flex-start;
                    align-items: center;
                    padding-top: 41px;
                    padding-bottom: 10px;
                    min-height: 848px;
                    height: auto;

                    ul {
                        margin-top: 24px;

                        li {
                            &::before {
                                content: url(../img/service/ul-arr.svg);
                                left: 0;
                                top: 3px;
                                width: initial;
                                height: initial;
                            }
                        }


                    }
                }


            }


            .container {
                position: relative;
                max-width: 1520px;
                display: flex;
                z-index: 2;
            }

            &-content {
                max-width: 603px;

                p {
                    font-family: Manrope;
                    font-style: normal;
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 185%;
                    color: #242B2F;
                    padding-left: 5px;

                    b {
                        font-weight: 600;
                    }
                }

                ol,
                ul {
                    margin-top: 32px;
                    margin-bottom: 29px;
                    counter-reset: list;
                    padding-left: 5px;

                    li {
                        font-family: Manrope;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 185%;
                        color: #242B2F;
                        margin-bottom: 29px;
                        padding-left: 56px;
                        position: relative;

                        span {
                            font-weight: 600;
                        }

                        &:before {
                            counter-increment: list;
                            content: counter(list);
                            position: absolute;
                            left: 0px;
                            top: 0px;
                            font-family: Manrope;
                            font-style: normal;
                            font-weight: 800;
                            font-size: 14px;
                            line-height: 32px;
                            letter-spacing: 0.02em;
                            color: #242B2F;
                            width: 32px;
                            height: 32px;
                            text-align: center;
                        }

                        &:after {
                            content: '';
                            position: absolute;
                            left: 4px;
                            top: 4px;
                            width: 24px;
                            height: 24px;
                            border: 2px solid #242B2F;
                            border-radius: 4px;
                            transform: rotate(45deg);
                        }
                    }
                }
            }

            &-title {
                margin-bottom: 16px;
                position: relative;

                svg {
                    position: absolute;
                    left: -32px;
                    top: 16px;
                    transform: translateX(-100%);
                }
            }

