        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 auto;
            margin-top: 121px;
            margin-bottom: 120px;
            padding-left: 7px;
            position: relative;

            .block-arrow__right {
                position: absolute;
                top: 23px;
                left: calc(50% + 111px);
                height: 144px;
                width: calc(40% - 177px);
                border-right: 2px solid #242B2F;
                border-top: 2px solid #242B2F;
                border-top-right-radius: 40px;

                span {
                    position: absolute;
                    left: -1px;
                    top: 2px;
                    width: 10px;
                    height: 2px;
                    border-radius: 1px;
                    background-color: #242B2F;
                    transform: rotate(-135deg);

                    &:last-of-type {
                        transform: rotate(135deg);
                        top: -6px;
                    }
                }

                @media (max-width: 990px) {
                    height: 91px;
                    width: calc(43% - 177px);
                }

                @media (max-width: 575px) {
                    top: 20px;
                    border-top-right-radius: 25px;
                    left: calc(50% + 90px);
                    width: calc(66% - 156px);
                }
            }

            .block-arrow__left {
                position: absolute;
                bottom: 0px;
                left: calc(43% + 1px);
                height: 50%;
                width: calc(59% - 1px);
                border-left: 2px solid #242B2F;
                border-bottom: 2px solid #242B2F;
                border-bottom-left-radius: 40px;

                &-wrap {
                    position: absolute;
                    top: -119px;
                    left: 0px;
                    height: 144px;
                    width: calc(50vw - 127px);

                    @media (max-width: 990px) {
                        top: -69px;
                        left: 0px;
                        height: 92px;
                        width: calc(50vw - 114px);
                    }

                    @media (max-width: 575px) {
                        top: -44px;
                        height: 67px;
                        width: calc(50vw - 90px);
                    }
                }

                &-top {
                    position: absolute;
                    bottom: 50%;
                    left: 0px;
                    height: 50%;
                    width: calc(43% + 3px);
                    border-right: 2px solid #242B2F;
                    border-top: 2px solid #242B2F;
                    border-top-right-radius: 40px;

                    @media (max-width: 990px) {
                        width: calc(45% + 1px);
                    }

                    @media (max-width: 575px) {
                        width: calc(19% + 1px);
                    }
                }

                span {
                    position: absolute;
                    right: -1px;
                    bottom: 2px;
                    width: 10px;
                    height: 2px;
                    border-radius: 1px;
                    background-color: #242B2F;
                    transform: rotate(45deg);

                    &:last-of-type {
                        transform: rotate(-45deg);
                        bottom: -6px;
                    }
                }

                @media (max-width: 990px) {
                    width: calc(56% - 1px);
                    left: calc(44% + 1px);
                }

                @media (max-width: 575px) {
                    width: calc(81% - 1px);
                    left: calc(19% - 1px);
                    border-bottom-left-radius: 25px;
                }
            }

            @media (max-width: 990px) {
                margin-top: 69px;
                padding-right: 0px;
                margin-bottom: 63px;
            }

            @media (max-width: 575px) {
                margin-top: 61px;
                padding-left: 0px;
                margin-bottom: 39px;
            }
        }
    }
}