.custom-checkbox {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  user-select: none; /* Отключение выделения текста */
}

.custom-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 24px;
  width: 24px;
  border: 2px solid #242B2F;
  display: flex;
  align-items: center;
  justify-content: center;
}

.custom-checkbox input:checked ~ .checkmark {
  border-color: #242B2F; /* Стилизация при выборе */
}

.custom-checkbox input:checked ~ .checkmark:after {
  content: "\2713"; /* Знак "галочка" */
  display: block;
  text-align: center;
  line-height: 1;
  font-size: 20px;
  color: #242B2F;
}
@media (max-width: $mobile-sm) {
  .custom-checkbox  {
    width: 16px;
    height: 16px;
  }
  .checkmark {
    width: 16px;
    height: 16px;
  }
  .custom-checkbox input:checked ~ .checkmark:after {
    font-size: 16px;
  }
}