.form {
  &__label {
    font-weight: 600;
    line-height: 140%;
    letter-spacing: 0.02em;
    font-feature-settings: 'tnum'on, 'lnum'on;
    margin-bottom: 16px;
    display: inline-block;

    & span {
      font-weight: 400;
      font-size: 16px;
    }
  }

  &__control {
    font-size: 16px;
    line-height: 140%;
    display: block;
    width: 100%;
    padding: 24px;
    background: linear-gradient(180deg, rgba(224, 213, 208, 0.52) 0%, rgba(224, 213, 208, 0.26) 100%);
    border-radius: 8px;
    border: none;
    opacity: .4;

    &:focus-visible {
      opacity: 1 !important;
    }
  }
}

@media (max-width: 768px) {
  .form {
    &__label {
      font-size: 16px;
    }
  }
}

@media (max-width: $mobile) {
  .form {
    &__label {
      font-size: 12px;
      margin-bottom: 8px;

      & span {
        font-size: 12px;
      }
    }

    &__control {
      font-size: 12px;
      padding: 16px;
    }
  }
}