.videoPlay {
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
  transition-property: background;
}
.videoPlay svg {
  margin-left: 5px;
}
.videoPlay:hover {
  background-color: #009dff;
}

.training__item .additional-title1 {
  font-weight: 200;
}
.training__item-info {
  display: flex;
  margin-top: 21px;
}
.training__item-left {
  width: 50%;
  max-width: 460px;
  margin-right: 12.5%;
}
.training__item-right {
  max-width: 480px;
}
.training__item:not(:last-of-type) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 96px;
  margin-bottom: 22px;
}
@media (max-width: 760px) {
  .training__item-info {
    flex-direction: column;
    margin-top: 12px;
  }
  .training__item-info .title4 {
    letter-spacing: 0;
  }
  .training__item-left {
    width: 100%;
  }
  .training__item-right {
    margin-top: 16px;
  }
  .training__item:not(:last-of-type) {
    padding-bottom: 28px;
    margin-bottom: 27px;
  }
}

.questDrop__dropdown {
  padding-bottom: 48px;
  margin-bottom: 47px;
  border-bottom: 1px solid rgba(236, 235, 247, 0.09);
}
.questDrop__dropdown-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding-right: 12px;
}
.questDrop__dropdown-title-text {
  font-weight: 600;
  font-size: 24px;
  line-height: 34px;
}
.questDrop__dropdown-title svg {
  transition: 0.3s;
}
.questDrop__dropdown-title.active svg {
  transform: rotate(180deg);
}
.questDrop__dropdown-contant {
  display: none;
  margin-top: 24px;
}
.questDrop__dropdown-contant-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 36px;
  letter-spacing: 0.01em;
}
.questDrop__dropdown-contant-text li {
  list-style-type: square;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 36px;
  letter-spacing: 0.01em;
  margin-top: 16px;
}
@media (max-width: 760px) {
  .questDrop__dropdown-contant-text li {
    font-size: 12px;
    line-height: 18px;
    margin-top: 12px;
  }
}
.questDrop__dropdown-contant ul {
  padding-left: 13px;
}
.questDrop__dropdown:last-child {
  border-bottom: none;
  margin-bottom: 0px;
  padding-bottom: 0px;
}
@media (max-width: 990px) {
  .questDrop__dropdown {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  .questDrop__dropdown-title {
    align-items: flex-start;
    padding-right: 0px;
  }
  .questDrop__dropdown-title-text {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    padding-right: 30px;
    max-width: calc(100% - 30px);
  }
  .questDrop__dropdown-title svg {
    margin-top: 5px;
  }
  .questDrop__dropdown-contant {
    margin-top: 17px;
  }
  .questDrop__dropdown-contant-text {
    font-size: 12px;
    line-height: 22px;
  }
}
@media (max-width: 560px) {
  .questDrop__dropdown-contant-text {
    font-size: 12px;
    line-height: 18px;
  }
}

.whom-item {
  display: flex;
  justify-content: space-between;
  padding-top: 48px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
.whom-item__name {
  max-width: 365px;
  margin-right: 40px;
  width: 50%;
}
.whom-item .whom-item__text {
  letter-spacing: 0.01em;
  width: 66.2%;
}
.whom-item:not(:last-of-type) {
  margin-bottom: 87px;
}
@media (max-width: 990px) {
  .whom-item .whom-item__text {
    font-size: 16px;
    line-height: 28px;
  }
}
@media (max-width: 760px) {
  .whom-item {
    flex-wrap: wrap;
    padding-top: 25px;
  }
  .whom-item:not(:last-of-type) {
    margin-bottom: 35px;
  }
  .whom-item .whom-item__text {
    font-size: 14px;
    line-height: 24px;
    width: 100%;
    margin-top: 11px;
  }
  .whom-item .whom-item__name {
    margin-right: 0px;
    width: 100%;
    font-size: 16px;
    line-height: 22px;
  }
}

.project-double {
  display: flex;
  align-items: center;
  justify-content: center;
}
.project-double.white span {
  background: white;
}
.project-double span {
  height: 52px;
  width: 21px;
  transform: skewX(-45deg);
  border-radius: 6px 0;
  background: #fff;
}
.project-double span + span {
  margin-left: 7px;
}
@media (max-width: 990px) {
  .project-double span {
    height: 46px;
    width: 17px;
  }
  .project-double span + span {
    margin-left: 9px;
  }
}
@media (max-width: 575px) {
  .project-double span {
    height: 16px;
    width: 6px;
    border-radius: 2px 0;
  }
  .project-double span + span {
    margin-left: 3px;
  }
}

[data-whatintent=touch] *:focus,
[data-whatintent=mouse] *:focus {
  outline: none;
}

* {
  box-sizing: border-box;
  outline: none;
}

*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Manrope", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 180%;
  color: #242B2F;
  overflow-x: hidden;
  height: 100% !important;
  cursor: default;
}
@media (max-width: 1600px) {
  body {
    font-size: 20px;
  }
}
@media (max-width: 990px) {
  body {
    font-size: 16px;
    line-height: 182%;
  }
}
@media (max-width: 760px) {
  body {
    line-height: 184%;
    font-size: 12px;
  }
}
@media (max-width: 480px) {
  body {
    overflow-x: hidden;
    max-width: 100vw;
  }
}

main {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin: 0;
}
ul.no-style,
ol.no-style {
  padding: 0;
  list-style: none;
}

button {
  border: none;
  cursor: pointer;
  font-family: "Manrope", sans-serif;
}

img {
  border: none;
}

a {
  text-decoration: none;
  color: inherit;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  transition: opacity 0.25s;
}

[contenteditable=true]:empty:before {
  content: attr(data-placeholder);
  color: #131313;
  font-size: inherit;
  display: block;
  /* For Firefox */
}

select {
  appearance: none;
}
select::-ms-expand {
  display: none;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

b {
  font-weight: 700;
}

blockquote {
  margin: 0;
  padding-left: 22px;
  border-left: 2px solid #242B2F;
  font-style: italic;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  padding-top: 4px;
