@keyframes arr-s {
  from {
    height: 0;
    width: 2px;
  }
  17% {
    width: 2px;
    height: 20vw;
  }
  58% {
    width: 20vw;
    height: 20vw;
  }
  to {
    height: 20vw;
    width: 20vw;
  }
}
@keyframes arr-sp-s {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.arrow-small {
  max-height: 204px;
  max-width: 103px;
  position: relative;
  border-left: 2px solid #242B2F;
  border-top: 2px solid #242B2F;
  border-radius: 40px 0 0 0;
  width: 20vw;
  height: 20vw;
  animation: arr-s 1.2s ease-in;
  animation-duration: 2s;
  transform-origin: center;
  animation-play-state: paused;
}
.arrow-small span {
  position: absolute;
  top: -6px;
  right: 1px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #242B2F;
  border-right: 2px solid #242B2F;
  transform: rotate(45deg);
  opacity: 1;
  transition: opacity 1s;
  animation: arr-sp-s 1.2s ease-in-out;
  animation-duration: 2s;
  animation-play-state: paused;
}
.arrow-small_pause {
  animation-play-state: initial;
}
