@keyframes rot {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.project6 {
    font-family: "IBMPlexSans", "san-serif";
    &__title {
        font-weight: 700;
        font-size: 76.0417px;
        line-height: 128%;
        color: #0fb56f;
        position: relative;
        text-transform: uppercase;
        &::before {
            content: "";
            position: absolute;
            width: 30px;
            height: 30px;
            top: -21%;
            left: -5%;
            background-image: url(../img/project6/desktop/corner.svg);
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
    &__h2 {
        font-weight: 500;
        font-size: 28px;
        line-height: 128%;
        color: rgba(131, 91, 243, 0.5);
    }
    & img {
        max-width: 100%;
    }
    & .ball {
        position: absolute;
        & img {
            max-width: 100%;
        }
    }
    & .project {
        &-hero {
            height: 1192px;
            background-color: transparent;
            background-image: url(../img/project6/hero/fon.png);
            background-position: top;
            & .ball {
                &:nth-of-type(1) {
                    left: -3.8%;
                    width: 7.7%;
                    top: 25.2%;
                    height: 13.25%;
                    & img {
                        mix-blend-mode: hard-light;
                        opacity: 0.9;
                        filter: blur(3px);
                        transform: matrix(1, 0, 0, -1, 0, 0);
                    }
                    &::after {
                        content: "";
                        position: absolute;
                        background: #26c5a8;
                        opacity: 0.3;
                        left: 18%;
                        top: -13%;
                        filter: blur(40px);
                        transform: matrix(0.69, 0.72, 0.58, -0.81, 0, 0);
                        width: 47%;
                        height: 69%;
                    }
                }
                &:nth-of-type(2) {
                    left: 21%;
                    top: 9%;
                    width: 6%;
                    filter: blur(1px)
                        drop-shadow(-16px -35px 16px rgba(38, 197, 168, 0.3));
                    transform: matrix(1, 0, 0, -1, 0, 0);
                }
                &:nth-of-type(3) {
                    right: 11.5%;
                    top: 3%;
                    width: 8%;
                    filter: blur(1px)
                        drop-shadow(0 -17px 10px rgba(38, 197, 168, 0.3));
                    transform: matrix(1, 0, 0, -1, 0, 0);
                }
                &:nth-of-type(4) {
                    right: 2%;
                    top: 15%;
                    width: 12%;
                    & img {
                        mix-blend-mode: hard-light;
                        opacity: 0.8;
                        filter: blur(3px);
                        transform: matrix(1, 0, 0, -1, 0, 0);
                    }
                    &::after {
                        content: "";
                        position: absolute;
                        background: #26c5a8;
                        opacity: 0.3;
                        left: 0;
                        top: -7%;
                        filter: blur(40px);
                        transform: matrix(0.69, 0.72, 0.58, -0.81, 0, 0);
                        width: 45%;
                        height: 83%;
                    }
                }
            }
            &__logo {
                max-width: 13.2%;
                position: absolute;
                top: 23%;
                right: 7%;
            }
            &__item {
                position: absolute;
                &:nth-of-type(1) {
                    width: 70%;
                    bottom: 9%;
                    left: 13%;
                }
                &:nth-of-type(2) {
                    width: 20%;
                    bottom: 9%;
                    right: 12%;
                }
                &:nth-of-type(3) {
                    width: 57%;
                    bottom: 24%;
                    left: 5%;
                }
            }
        }
        &-main {
            margin-top: 1192px;
            &__wp {
                display: grid;
                grid-template-columns: 460px minmax(320px, 1460px);
                grid-template-rows: auto auto;
                position: relative;
            }

            &__left {
                grid-row: 1/3;
                width: auto;
                z-index: 4;
            }
            &__left-link {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                margin-top: 13px;
            }
            &__right {
                padding-top: 0;
                width: 100%;
            }
            &__sect:nth-child(odd) {
                padding: 120px 0px;
                background-image: url(../img/project/line-back.png);
                background-position: 100% 0;
                background-size: 100% auto;
                & .left {
                    height: 174px;
                }
                & .right a {
                    display: flex;
                    align-items: center;
                    font-style: normal;
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 140%;
                    color: #b30b00;
                    margin-top: 40px;
                    transition: 0.25s;
                    & svg {
                        margin-right: 16px;
                    }
                }
            }
            &__sect:nth-child(even) {
                padding: 50px 0px 117px;
                & .left {
                    margin-top: -20px;
                    height: 278px;
                }
                & .right .main-hero__bottom-left {
                    margin-top: 57px;
                    & span {
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 140%;
                    }
                }
                & .main-hero__bottom-right {
                    margin-top: 75px;
                    & a {
                        margin-left: 0;
                        margin-right: 61px;
                        font-weight: 600;
                    }
                }
                & .right {
                    max-width: 501px;
                    margin-top: -22px;
                    & span {
                        font-weight: 300;
                        font-size: 24px;
                        line-height: 140%;
                        letter-spacing: 0.02em;
                    }
                    & p {
                        font-size: 24px;
                        line-height: 180%;
                        font-weight: 600;
                        letter-spacing: 0.02em;
                    }
                }
            }
        }
    }
