.vacancy {
  & .works-hero {
    padding-bottom: 58px;

    &__wp {
      & h2 {
        margin-top: 0;
      }
    }
  }

  &__bg::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: linear-gradient(180deg, rgba(224, 213, 208, 0.52) 0%, rgba(224, 213, 208, 0.26) 100%);
    opacity: .4;
  }

  &__title {
    font-weight: 600;
    font-size: 64px;
    line-height: 120%;
    letter-spacing: 0.02em;
    margin-bottom: 64px;
  }
}

.about-vacancy {
  padding: 80px 0px 120px;

  &__introduction {
    max-width: 1200px;
    display: flex;
    gap: 100px;
    margin: 0 auto;
    padding: 0px 88px 64px;
    box-sizing: content-box;

    & p {
      font-weight: 700;
      font-size: 24px;
      line-height: 180%;
    }

    & svg {
      max-width: 156px;
      width: 100%;
      height: 100%;
    }
  }

  &__img {
    max-width: 1600px;
    margin: 0 auto;
    display: flex;

    & img {
      max-width: 100%;
    }

  }

  &__body {
    padding: 120px 180px;
    position: relative;
  }

  &__block {
    display: flex;
    row-gap: 88px;
    max-width: 1400px;
    margin: 0 auto;
    column-gap: 40px;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__column {
    max-width: 600px;


    &:last-child {
      max-width: 100%;
    }

    & p {
      font-weight: 600;
      font-size: 40px;
      line-height: 140%;
      margin-bottom: 16px;
    }

    & span {
      font-weight: 300;
      font-size: 24px;
      line-height: 180%;
    }
  }

  &__block-list {
    padding-top: 40px;
    display: flex;
    row-gap: 50px;
    column-gap: 40px;
    justify-content: space-between;

    & ul {
      max-width: 600px;
    }

    & li {
      font-weight: 300;
      font-size: 24px;
      line-height: 180%;

      &::marker {
        font-size: 18px;
      }
    }
  }
}

.vacancy-form {
  padding: 120px 60px;

  &__body {
    max-width: 1200px;
    box-sizing: content-box;
    padding: 0px 88px;
    margin: 0 auto;
  }

  &__block {}

  &__input-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 32px;

    & div {
      width: 500px;

      &:last-child {
        min-width: 100%;
      }
    }
    & div.wrong {
        & input {
            background: linear-gradient(180deg,rgba(255, 179, 179, 0.52),rgba(255, 82, 82, 0.26));
        }
    }
  }

  &__bottom {
    padding-top: 80px;
    display: flex;
    justify-content: space-between;

    & button {
      background: transparent;
    }

    & .btn {
      justify-content: center;
      width: 209px;
    }
  }
}

.vacancy-jobs {
  padding: 120px 60px 200px;
  position: relative;

  &__wrapper {
    max-width: 1600px;
    margin: 0 auto;
  }

  &__body {
    display: flex;
    flex-direction: column;
    row-gap: 24px;
  }
}

@media (max-width: 1600px) {
  .about-vacancy {
    &__column {
      flex: 1 1 600px;
      max-width: 100%;
    }
  }
}

@media (max-width: 1440px) {
  .vacancy-jobs {
    padding: 100px 32px 160px;
  }

  .about-vacancy {
    &__body {
      padding: 100px 152px;
    }
  }

  .vacancy-form {
    padding: 100px 32px;
  }
}

@media (max-width: 1300px) {
  .vacancy-form {
    &__body {
      padding: 0px;
      box-sizing: border-box;
    }

    &__block {
      padding: 0px 88px;

    }

    &__input-block {

      & div {
        width: 100%;
      }
    }

  }

}

@media (max-width: $desktop) {
  .vacancy {
    &__title {
      font-size: 56px;
      margin-bottom: 56px;
    }
  }

  .about-vacancy {
    padding: 64px 0px;

    &__introduction {
      gap: 80px;

      & p {
        font-size: 20px;
      }
    }

    &__body {
      padding: 100px 160px;
    }

    &__block {
      gap: 60px;
    }

    &__block-list {
      flex-wrap: wrap;
      gap: 0;

      & ul {
        max-width: 100%;

        & li {
          font-size: 20px;

          &::marker {
            font-size: 16px;
          }
        }
      }
    }

    &__column {
      & p {
        font-size: 36px;
      }

      & span {
        font-size: 20px;
      }
    }
  }
}

@media (max-width: $tablet) {
  .vacancy {
    &__title {
      font-size: 48px;
      margin-bottom: 48px;
    }
  }

  .about-vacancy {
    &__introduction {
      gap: 60px;

      & p {
        font-size: 18px;
      }
    }

    &__body {
      padding: 80px 140px;
    }

    &__block {
      gap: 50px;
    }

    &__block-list {
      & ul {
        & li {
          font-size: 18px;
        }
      }
    }

    &__column {
      & p {
        font-size: 32px;
      }

      & span {
        font-size: 18px;
      }
    }
  }

  .vacancy-form {
    padding: 80px 32px;
  }
}

@media (max-width: 768px) {
  .vacancy {
    &__title {
      font-size: 40px;
      margin-bottom: 40px;
    }
  }

  .about-vacancy {
    &__introduction {
      gap: 40px;
      max-width: 528px;
      margin: 0 auto;
      padding: 0px 0px 64px;

      & p {
        font-size: 16px;
      }

      & svg {
        max-width: 88px;
      }
    }

    &__body {
      padding: 80px 32px;
    }

    &__block {
      max-width: 528px;
      gap: 40px;
    }

    &__block-list {
      & ul {
        & li {
          font-size: 16px;

          &::marker {
            font-size: 14px;
          }
        }
      }
    }

    &__column {
      & p {
        font-size: 24px;
      }

      & span {
        font-size: 16px;
      }
    }
  }

  .vacancy-jobs {
    padding: 80px 32px;
  }

  .vacancy-form {
    padding: 80px 32px;

    &__input-block {
      gap: 24px;
    }

    &__bottom {
      padding-top: 64px;

      & .btn {
        max-width: 199px;
      }
    }
  }
}

@media (max-width: 760px) {
  .vacancy-jobs {
    padding: 50px 16px 80px;
  }

  .about-vacancy {
    &__body {
      padding: 80px 16px;
    }
  }

  .vacancy-form {
    padding: 80px 16px;

    &__block {
      padding: 0;
    }
  }
}

@media (max-width: $mobile) {
  .vacancy {
    &__title {
      font-size: 32px;
      margin-bottom: 32px;
    }
  }

  .about-vacancy {
    &__introduction {
      gap: 30px;
      padding: 0px 0px 40px;

      & p {
        font-size: 14px;
      }
    }

    &__body {
      padding: 60px 16px;
    }

    &__block {
      row-gap: 24px;
    }

    &__block-list {
      padding-top: 16px;

      & ul {
        padding-left: 20px;

        & li {
          font-size: 14px;

          &::marker {
            font-size: 12px;
          }
        }
      }
    }

    &__column {
      & p {
        font-size: 20px;
        margin-bottom: 8px;
      }

      & span {
        font-size: 14px;
      }
    }
  }

  .vacancy-form {
    padding: 60px 16px;

    &__bottom {
      padding-top: 36px;

      & .btn {
        max-width: 150px;
      }
    }
  }
}

@media (max-width: $mobile-sm) {
  .vacancy {
    &__title {
      font-size: 24px;
      margin-bottom: 24px;
    }
  }

  .about-vacancy {
    padding: 40px 0px;

    &__introduction {
      gap: 16px;

      & p {
        font-size: 12px;
      }

      & svg {
        min-width: 48px;
      }
    }

    &__body {
      padding: 40px 16px;
    }

    &__block-list {
      padding-top: 8px;

      & ul {

        & li {
          font-size: 12px;
        }
      }
    }

    &__column {
      & p {
        font-size: 16px;

      }

      & span {
        font-size: 12px;
      }
    }
  }

  .vacancy-form {
    padding: 40px 16px;

    &__input-block {
      gap: 16px;
    }

    &__bottom {
      padding-top: 24px;
      flex-wrap: wrap;
      row-gap: 40px;

      & .btn {
        max-width: 114px;
      }

      & button {
        margin: 0 auto;
        width: 100%;
      }
    }
  }
}
