.contacts {
    .works-hero {
        padding-top: 117px;

        &__wp {
            margin-left: 18px;
            margin-top: 1px;

            h2 {
                margin-top: -2px;
                margin-left: 1px;
            }
        }
    }

    &-main {
        display: flex;
        padding-top: 120px;
        padding-bottom: 150px;

        &__wrap {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            max-width: 1243px;
            margin: 0 auto;
            padding: 2px 0 0 2px;
        }

        &__left {
            max-width: 485px;
            margin-left: 22px;
            width: 100%;
        }

        &__title {
            font-family: Manrope;
            font-style: normal;
            font-weight: 300;
            font-size: 24px;
            line-height: 140%;
            letter-spacing: 0.02em;
            color: $text-color;
        }

        &__uptitle {
            font-family: Manrope;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 180%;
            letter-spacing: 0.02em;
            color: $text-color;
            margin-bottom: 32px;
            margin-top: 3px;
        }

        &__right {
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: 521px;
            width: 100%;
            margin-left: 50px;

            &-el-mob {
                display: flex;
                align-items: center;
                justify-content: flex-start;

                svg, img {
                    margin-right: 10px;
                }

                p {
                    font-family: Manrope;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 140%;
                    color: $text-color;
                }
            }

            &-mob {
                display: none;
                justify-content: space-between;
                align-items: center;
                margin-top: 74px;
                max-width: 350px;
            }
        }

        &__adress {
            margin-bottom: 37px;
            display: flex;
            margin-left: -1px;

            img {
                height: 18px;
                margin-top: 2px;
            }

            div {
                margin-left: 16px;

                p {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 140%;
                    position: relative;
                    margin-bottom: 4px;
                    &:first-child {
                        &::before {
                            opacity: 0;
                        }
                    }
                    &::before {
                        content: "";
                        position: absolute;
                        bottom: -8px;
                        left: 0;
                        width: 100%;
                        height: 2px;
                        transition: .25s;
                        background: $light-green;
                    }

                    @media (max-width: $tablet) {
                        margin-bottom: 8px;
                    }

                    @media (max-width: $mobile-big) {

                    }
                }
            }
        }

        &__tels {
            margin-bottom: 42px;
            margin-left: -2px;
            display: flex;
            a {
                margin-right: 61px;
                display: flex;
                font-weight: 600;
                align-items: center;
                span {
                    position: relative;
                    margin-left: 13px;
                    white-space: nowrap;
                    transition: 0.25s;
                    &::before {
                        content: "";
                        position: absolute;
                        bottom: -10px;
                        left: 0px;
                        width: 100%;
                        height: 2px;
                        transition: .25s;
                        background: $pink;
                    }
                }
                &:last-child {
                    margin-right: 0;
                    img {
                        opacity: 0;
                    }
                }

                @media (hover: hover) {
                    &:hover {
                        color: $pink;
                        span::before {
                            background: $text-color;
                        }
                    }
                }
            }
        }

        &__img {
            display: block;
            width: 237px;
            margin-top: 11px;
            padding: 16px 16px 0;
            margin-left: -16px;
            transition: 0.2s;

            span {
                transition: 0.2s;
                opacity: 0;
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 140%;
                text-align: center;
                display: block;
                height: 38px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding-bottom: 7px;
            }

            img {
                width: 100%;
            }

            @media (hover: hover) {
                &:hover {
                    background: rgba(217, 217, 217, 0.4);

                    span {
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.contacts .main-hero__bottom {
    &-right {
        margin-top: 73px;
        width: 93.5%;
        display: flex;
        justify-content: space-between;
    
        a {
            color: $text-color;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 180%;
            letter-spacing: 0.5px;
            margin-left: 0px;
        }
    }

    &-left {
        a {
            margin-right: 75px;
    
            &:last-child {
                margin-right: 0px;
            }
    
            img {
                width: 16px;
            }
    
            span::before {
                bottom: -10px;
            }
        }
    }
}

@media (max-width: 1260px) {
    .contacts-main {
        &__tels {
            justify-content: space-between;
            max-width: 520px;
        }
    }
}

@media (max-width: $tablet) {
    .contacts {
        .works-hero {
            padding-top: 173px;
            padding-bottom: 44px;

            &__wp {
                margin: 1px 0 0;

                h2 {
                    margin: -2px 0 0;
                    font-size: 40px;
                    line-height: 140%;
                }
            }
        }

        &-main {
            padding-top: 82px;
            padding-bottom: 61px;
    
            &__wrap {
                padding: 0;
            }
    
            &__left {
                max-width: 524px;
                padding-right: 41px;
                margin: 0 auto;
            }
    
            &__uptitle {
                margin-bottom: 41px;
            }
    
            &__tels {
                margin-left: -2px;
                margin-bottom: 34px;
                max-width: 415px;
                justify-content: unset;
    
                a {
                    span {
                        margin-left: 10px;
                    }
                }
            }
    
            &__right {
                display: none;
    
                &-mob {
                    display: flex;
                    margin-top: 49px;
                    margin-left: -2px;
                    gap: 135px;
                }
            }
    
            &__adress {
                margin-bottom: 25px;
            }
        }
    }

    .contacts .main-hero__bottom {
        &-right {
            width: 95%;
            margin-top: 64px;
        }

        &-left {
            margin-left: 0;
            max-width: 415px;
            justify-content: unset;
            gap: 45px;
    
            a {
                margin-right: 0;
            }
        }
    }
}

@media (max-width: $mobile) {
    .contacts {
        .works-hero {
            padding-top: 79px;
            padding-bottom: 27px;

            &__wp {
                margin: 2px 0 0 1px;

                h2 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 106%;
                }

                svg {
                    width: 22px;
                }
            }
        }

        &-main {
            padding-top: 40px;
            padding-left: 48px;
            padding-bottom: 12px;
    
            &__left {
                padding-right: 0px;
                margin: 0px;
                max-width: 222px;
            }
    
            &__title {
                font-size: 16px;
                line-height: 140%;
            }
    
            &__uptitle {
                margin-top: 4px;
                font-size: 16px;
                line-height: 180%;
                margin-bottom: 40px;
            }
    
            &__right {
                &-mob {
                    flex-direction: column;
                    align-items: flex-start;
                    margin-top: 72px;
                }
    
                &-el-mob {
                    align-items: flex-start;
                    margin-bottom: 41px;
    
                    svg {
                        width: 16px;
                        height: 16px;
                        margin-right: 8px;
                    }
    
                    p {
                        font-size: 12px;
                        line-height: 140%;
                    }
    
                    &:last-child {
                        margin-bottom: 0px;
                    }
                }
            }
    
            &__adress {
                margin-bottom: 37px;
    
                div {
                    margin: 1px 0 0 7px;
    
                    p {
                        font-size: 12px;
                        line-height: 140%;
    
                        &::before {
                            bottom: -9px;
                        }

                        &:first-child {
                            &::before {
                                opacity: 1;
                            }
                        }

                        &:last-child {
                            margin-top: 17px;
                        }
                    }
                }
    
                img {
                    margin-top: -1px;
                }
            }
    
            &__tels {
                flex-direction: column;
                align-items: initial;
                margin-bottom: 30px;
    
                a {
                    margin-top: -7px;
    
                    span {
                        font-size: 12px;
                        line-height: 140%;
                        margin: 2px 0 0 4px;
    
                        &::before {
                            bottom: -8px;
                        }
                    }
    
                    &:last-child {
                        margin-top: 13px;
                    }
                }
            }
        }
    }

    .contacts .main-hero__bottom {
        &-left {
            flex-direction: column;
            align-items: initial;
    
            a {
                font-size: 12px;
                line-height: 140%;
    
                span {
                    margin-left: 8px;
                }
    
                
                &:last-child {
                    margin-top: -12px;
                }
            }
        }

        &-right {
            width: 110%;
            max-width: 240px;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 45px;
            margin-bottom: 12px;
    
            a {
                min-width: 100px;
                font-size: 16px;
                line-height: 140%;
                margin-bottom: 18px;
                width: 47%;
            }
        }
    }

    .contacts-main__right-mob {
        margin-left: 0px;
        margin-top: 48px;
        gap: 24px;

        .contacts-main__right-el-mob {
            margin-bottom: 0;
            img {
                width: 16px;
            }
        }
    }
}
