.about {
    &-logo__img {
        width: 100%;
        max-height: 490px;
        object-fit: contain;
    }

    &-wrapper {
        width: 100%;
        max-width: 1664px;
        padding: 0 32px;
        margin: 0 auto;

        &__text {
            gap: 80px;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            margin: 120px 40px 0;
            color: $text-color;
            font-style: normal;
            font-weight: 300;
            font-size: 24px;
            line-height: 180%;
    
            &-elem {
                width: 600px;
            }
        }
    
        &__office {
            margin-top: 120px;

            img {
                width: 100%;
                object-fit: contain;
                max-height: 700px;
            }
        }

        &__jobs {
            margin: 120px 0 200px;

            &-title {
                color: $text-color;
                font-style: normal;
                font-weight: 600;
                font-size: 64px;
                line-height: 120%;
                margin-bottom: 64px;
            }

            &-elem {
                display: flex;
                align-items: center;
                position: relative;
                justify-content: space-between;
                margin-top: 24px;
                padding-bottom: 24px;
                border-bottom: 2px solid $text-color;
            }
        }

        &__job {
            &-title-and-time {
                width: 55%;
                position: relative;
            }

            &-title {
                font-style: normal;
                font-weight: 600;
                font-size: 40px;
                line-height: 140%;
            }

            &-time {
                text-align: center;
                position: absolute;
                right: 0px;
                bottom: 1px;
                font-style: normal;
                font-weight: 300;
                font-size: 24px;
                line-height: 180%;
            }

            &-arrow {
                position: absolute;
                bottom: 0;
                right: 220px;
            }
        }
    }

    &-team {
        margin-top: 120px;
        width: 100%;
        background: linear-gradient(180deg, rgba(224, 213, 208, 0.52) 0%, rgba(224, 213, 208, 0.26) 100%);

        &__title {
            padding-top: 120px;
            color: $text-color;
            font-style: normal;
            font-weight: 600;
            font-size: 64px;
            line-height: 120%;
        }

        &__wr {
            margin-top: 64px;
            padding-bottom: 80px;
            display: flex;
            flex-wrap: wrap;
        }

        &__person {
            width: 300px;
            margin-right: 100px;
            margin-bottom: 40px;

            &-name {
                margin-top: 16px;
                color: $text-color;
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 140%;
                letter-spacing: 0.02em;
            }

            &-post {
                color: $text-color;
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 140%;
            }
        }
    }
}

@media (max-width: 1400px) {
    .about-wrapper__job-arrow {
        display: none;
    }

    .about {
        &-wrapper {
            &__job {
                &-title-and-time {
                    width: 65%;
                }

                &-time {
                    text-align: right;
                }
            }
        }
    }
}

@media (max-width: $tablet) {
    .about {
        &-logo {
            img {
                width: auto;
                max-height: 360px;
                transform: translate(-20%);
            }
        }

        &-team {
            margin: 64px 0 80px 0;

            &__person {
                margin-right: 35px;
                margin-bottom: 50px;

                &-name {
                    margin-top: 7px;
                }

                &-post {
                    margin-top: 6px;
                }
            }

            &__title {
                padding-top: 82px;
                font-size: 40px;
            }

            &__wr {
                margin-top: 40px;
                margin-left: 32px;
                padding-bottom: 39px;
            }
        }

        &-wrapper {
            &__text {
                font-size: 16px;
                margin-top: 56px;
                margin-left: 46px;
                flex-direction: column;
                gap: 40px;

                &-elem {
                    width: auto;
                    max-width: 534px;
                    margin: 0 auto;
                }
            }

            &__office {
                margin-top: 64px;
            }

            &__jobs {
                margin-top: 80px;
                margin-bottom: 136px;

                &-title {
                    font-size: 40px;
                    margin-bottom: 37px;
                }

                &-elem {
                    margin-top: 20px;
                    padding-bottom: 16px;
                }
            }

            &__job {
                &-title-and-time {
                    width: 70%;
                }

                &-title {
                    font-size: 24px;
                }

                &-time {
                    font-size: 16px;
                    left: calc(50% - 16px);
                }
            }
        }
    }

    .works-hero__wp {
        h2 {
            margin-top: 1px;
        }
    }
}

@media (max-width: $mobile) {
    .about {
        &-logo {
            img {
                max-height: 220px;
                transform: translate(-38%);
            }
        }

        &-wrapper {
            padding: 0 16px;

            &__text {
                font-size: 12px;
                margin: 33px 0px 0px 0px;
                line-height: 183%;
                gap: 24px;
            }

            &__office {
                margin-top: 40px;
            }

            &__jobs {
                margin-top: 45px;
                margin-bottom: 78px;

                &-title {
                    font-size: 24px;
                    margin-bottom: 26px;
                }

                &-elem {
                    margin-top: 16px;
                    padding-bottom: 7px;
                }
            }

            &__job {
                &-title-and-time {
                    margin-bottom: 0px;
                    display: flex;
                    flex-direction: column;
                }

                &-title {
                    font-size: 16px;
                }

                &-time {
                    text-align: left;
                    position: static;
                    font-size: 12px;
                }
            }
        }

        &-team {
            margin: 41px 0 32px;

            &__title {
                padding-top: 44px;
                font-size: 24px;
            }

            &__wr {
                gap: 16px;
                flex-direction: row;
                margin-left: 0;
                margin-top: 26px;
            }

            &__person {
                width: 136px;
                margin-right: 0px;
                margin-bottom: 0px;

                img {
                    width: 136px;
                }

                &-name {
                    margin-top: 2px;
                    font-size: 16px;
                }

                &-post {
                    font-size: 12px;
                }
            }
        }
    }

    .works-hero__wp {
        h2 {
            margin-top: -5px;
        }
    }
}
