@mixin transition($element: all, $time: 0.25s, $function: linear) {
    transition: $time $function;
    transition-property: #{$element};
}

@mixin transition-cubic(
    $time: 0.25s,
    $function: cubic-bezier(1, 0, 0, 1),
    $element: all
) {
    transition: $element $time $function;
}

@mixin scrollbar($width: 2px, $rail-color: #ccc, $bar-color: #999) {
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    scrollbar-color: $bar-color $rail-color;
    scrollbar-width: thin;

    &::-webkit-scrollbar {
        width: $width;
        height: $width;
        background-color: $rail-color;
    }

    &::-webkit-scrollbar-track {
        background-color: $rail-color;
    }

    &::-webkit-scrollbar-thumb:vertical {
        background-color: $bar-color;
    }

    &::-webkit-scrollbar-thumb:horizontal {
        background-color: $bar-color;
    }
}

$maxWidth: 1920;
@mixin adaptiv-font($pcSize, $mobSize) {
    $addSize: $pcSize - $mobSize;
    $maxWidth: $maxWidth - 320;
    font-size: calc(
        #{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})
    );

    @media (min-width: 1920px) {
        font-size: $pcSize + px;
    }
}

.videoPlay {
    width: 80px;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.06);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.3s;
    transition-property: background;

    svg {
        margin-left: 5px;
    }

    &:hover {
        background-color: #009dff;
    }
}

.training {
    &__item {
        .additional-title1 {
            font-weight: 200;
        }

        &-info {
            display: flex;
            // justify-content: space-between;
            margin-top: 21px;
        }

        &-left {
            // width: 40%;
            width: 50%;
            max-width: 460px;
            margin-right: 12.5%;
        }

        &-right {
            // width: 40%;
            max-width: 480px;
        }

        &:not(:last-of-type) {
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            padding-bottom: 96px;
            margin-bottom: 22px;
        }
    }

    @media (max-width: 760px) {
        &__item {
            &-info {
                flex-direction: column;
                margin-top: 12px;

                .title4 {
                    letter-spacing: 0;
                }
            }

            &-left {
                width: 100%;
            }

            &-right {
                margin-top: 16px;
            }

            &:not(:last-of-type) {
                padding-bottom: 28px;
                margin-bottom: 27px;
            }
        }
    }
}

.questDrop {
    &__dropdown {
        padding-bottom: 48px;
        margin-bottom: 47px;
        border-bottom: 1px solid rgba(236, 235, 247, 0.09);

        &-title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            cursor: pointer;
            padding-right: 12px;

            &-text {
                font-weight: 600;
                font-size: 24px;
                line-height: 34px;
            }

            svg {
                transition: 0.3s;
            }

            &.active {
                svg {
                    transform: rotate(180deg);
                }
            }
        }

        &-contant {
            display: none;
            margin-top: 24px;

            &-text {
                font-weight: 300;
                font-size: 18px;
                line-height: 36px;
                letter-spacing: 0.01em;

                li {
                    list-style-type: square;
                    font-style: normal;
                    font-weight: 300;
                    font-size: 20px;
                    line-height: 36px;
                    letter-spacing: 0.01em;
                    margin-top: 16px;

                    @media (max-width: 760px) {
                        font-size: 12px;
                        line-height: 18px;
                        margin-top: 12px;
                    }
                }
            }

            ul {
                padding-left: 13px;
            }
        }

        &:last-child {
            border-bottom: none;
            margin-bottom: 0px;
            padding-bottom: 0px;
        }
    }

    @media (max-width: 990px) {
        &__dropdown {
            padding-bottom: 20px;
            margin-bottom: 20px;

            &-title {
                align-items: flex-start;
                padding-right: 0px;

                &-text {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 24px;
                    padding-right: 30px;
                    max-width: calc(100% - 30px);
                }

                svg {
                    margin-top: 5px;
                }
            }

            &-contant {
                margin-top: 17px;

                &-text {
                    font-size: 12px;
                    line-height: 22px;
                }
            }
        }
    }

    @media (max-width: 560px) {
        &__dropdown-contant-text {
            font-size: 12px;
            line-height: 18px;
        }
    }
}

.whom-item {
    display: flex;
    justify-content: space-between;
    padding-top: 48px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);

    &__name {
        max-width: 365px;
        margin-right: 40px;
        width: 50%;
    }

    .whom-item__text {
        letter-spacing: 0.01em;
        width: 66.2%;
    }

    &:not(:last-of-type) {
        margin-bottom: 87px;
    }

    @media (max-width: 990px) {
        .whom-item__text {
            font-size: 16px;
            line-height: 28px;
        }
    }

    @media (max-width: 760px) {
        flex-wrap: wrap;
        padding-top: 25px;

        &:not(:last-of-type) {
            margin-bottom: 35px;
        }

        .whom-item__text {
            font-size: 14px;
            line-height: 24px;
            width: 100%;
            margin-top: 11px;
        }

        .whom-item__name {
            margin-right: 0px;
            width: 100%;
            font-size: 16px;
            line-height: 22px;
        }
    }
}

.project-double {
    display: flex;
    align-items: center;
    justify-content: center;

    &.white {
        & span {
            background: white;
        }
    }

    & span {
        height: 52px;
        width: 21px;
        transform: skewX(-45deg);
        border-radius: 6px 0;
        background: #fff;

        & + span {
            margin-left: 7px;
        }
    }

    @media (max-width: 990px) {
        & span {
            height: 46px;
            width: 17px;

            & + span {
                margin-left: 9px;
            }
        }
    }

    @media (max-width: 575px) {
        & span {
            height: 16px;
            width: 6px;
            border-radius: 2px 0;

            & + span {
                margin-left: 3px;
            }
        }
    }
}
