.project5 {
  font-family: 'Montserrat', "san-serif";
  & .pixel {
    transition: .6s;
    position: absolute;
    & img {
      position: absolute;
      width: 100% ;
    }
  }
  &__h2 {
    font-weight: 600;
    font-size: 152.083px;
    line-height: 121%;
    color: $nb;

    &--white {
      color: #fff;
    }
  }

  .project {
    &-hero {
      height: 1192px;
      background-image: url(../img/project5/hero/fon2.png), url(../img/project5/hero/fon.webp);
      background-color: transparent;
      background-position: bottom;

      &__block {
        padding-left: 97px;
        padding-top: 265px;
        position: relative;
      }

      &__title {
        color: $nb;
        font-weight: 700;
        font-size: 200px;
        line-height: 122%;
        max-width: initial;
        margin-left: 0;
        padding-top: 0;
      }

      &__sub-title {
        color: $nb;
        font-weight: 700;
        font-size: 100px;
        line-height: 122%;
      }
    }

    &-main {
      margin-top: 1192px;

      &__wp {
        display: grid;
        grid-template-columns: 460px minmax(320px, 1460px);
        grid-template-rows: auto auto;
        position: relative;
      }

      &__left {
        grid-row: 1/3;
        width: auto;
      }
      &__left-link {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 13px;
      }
      &__right {
        padding-top: 0;
        width: 100%;
      }

      &__sect:nth-child(5) {
        padding-top: 117px;
        padding-bottom: 119px;

        & .right a {
          display: flex;
          align-items: center;
          font-style: normal;
          font-weight: 700;
          font-size: 16px;
          line-height: 140%;
          color: #B30B00;
          margin-top: 40px;
          transition: .25s;

          & svg {
            margin-right: 16px;
          }
        }

        & .left {
          height: 174px;
        }
      }

      &__sect:nth-child(6) {
        padding: 50px 0 117px;
        height: auto;
        background: none;

        & .project-main__arr {
          left: 41.1%;
        }

        & .main-hero__bottom-right {
          margin-top: 75px;

          & a {
            margin-left: 0;
            margin-right: 61px;
            font-weight: 600;
          }
        }

        & .left {
          margin-top: -20px;
          height: 278px;
        }

        & .right {
          max-width: 501px;
          margin-top: -22px;

          & .main-hero__bottom-left {
            margin-top: 57px;

            & span {
              font-weight: 600;
              font-size: 16px;
              line-height: 140%;
            }
          }

          & span {
            font-weight: 300;
            font-size: 24px;
            line-height: 140%;
            letter-spacing: .02em;
          }

          & p {
            font-size: 24px;
            line-height: 180%;
            font-weight: 600;
            letter-spacing: .02em;
          }
        }
      }

    }
  }

  &-sect {
    &__aboutUs {
      padding-top: 142px;
      padding-left: 99px;
      padding-bottom: 500px;
      position: relative;
      & .pixel {
        
        &:nth-of-type(1) {
          top: 16.5% ;
          right: 18%;
          width: 2%;
          height: 1%;
        }
        &:nth-of-type(2) {
          bottom: 19.5%;
          left: 11%;
          width: 1%;
          height: 1%;
        }
      }
      & .project5__h2 {
        position: relative;
        display: inline;
        &::after {
          content: '';
          position: absolute;
          top: 57%;
          left: 2.5%;
          width: 10px;
          height: 10px;
          background-image: url(../img/project5/aboutUs/pixel-1.svg);
          background-repeat: no-repeat;
          }
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          right: 29%;
          width: 22px;
          height: 37px;
          background-image: url(../img/project5/aboutUs/pixel-2.svg);
          background-repeat: no-repeat;
          }
      }
      & .project5-aboutUs {
        &__block {
          display: flex;
          & p {
            color: #001424;
              font-size: 28px;
              line-height: 150%;
              margin-top: 120px;
              max-width: 601px;
              margin-left: 101px;
          }
        }
        &__windows {
          position: relative;
          & img {
            position: absolute;
            max-width: 100%;
            &:nth-child(1) {
              width: 27%;
              bottom: -330px;
              right: 20%;
              z-index: 1;
            }
            &:nth-child(2) {
              width: 47%;
              bottom: -330px;
              right: 3px;
            }
            &:nth-child(3) {
              width: 10%;
              bottom: -252px;
              right: 1%;
              z-index: -1;
            }
          }
        }
      }
    }

    &__font {
      padding-top: 129px;
      position: relative;
      & .pixel {
        &:nth-of-type(1) {
          bottom: 25%;
          left: 28%;
          width: 7%;
          height: 1%;
        }
        &:nth-of-type(2) {
          bottom: 3%;
          right: 47%;
          width: 1.5%;
          height: 1%;
        }
        &:nth-of-type(3) {
          bottom: 0%;
          right: 28%;
          width: 1.5%;
          height: 1%;
        }
      }
      & .project5__h2 {
        margin-bottom: -50px;
        text-align: center;
        font-weight: 700;
        position: relative;
        & .pixel {
          &:nth-of-type(1) {
            top: -5%;
            left: 18%;
            width: 1%;
            height: 1%;
          }
          &:nth-of-type(2) {
            bottom: 12%;
            right: 14%;
            width: 1%;
            height: 1%;
          }
        }
      }

      & .project5-font {
        &__block {
          padding: 197px 60px 329px 100px;
          background-color: #6735FF;
          display: flex;
          justify-content: space-between;
          gap: 20px;
          color: #fff;
        }

        &__left {
          padding: 77px 0px 97px;

          & div {
            font-size: 86.5782px;
            line-height: 122%;
            margin-bottom: 56px;
          }

          & ul {
            list-style: none;
            padding: 0;
            display: flex;
            flex-direction: column;
            gap: 27.71px;

            & li {
              font-size: 34.6313px;
              line-height: 123%;
            }
          }
        }

        &__right {
          position: relative;
          flex-basis: 53%;
          background-image: url(../img/project5/font/line.svg);
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
            & span {
              padding-top: 47px;
              text-align: center;
              font-weight: 600;
              font-size: 259px;
              line-height: 120%;
              display: block;
            }

          & p {
            margin-top: 132px;
            font-weight: 600;
            font-size: 43px;
            line-height: 123%;
            letter-spacing: 0.22em;
            display: flex;
            justify-content: flex-end;
          }

          & img {
            position: absolute;
            top: 0;
            left: 0;
            max-width: 100%;
            height: 100%;
          }
        }
      }
    }

    &__colors {
      padding: 135px 60px 225px 100px;

      & .project5-colors {
        &__block {
          display: flex;
          flex-direction: column;
          // flex-wrap: wrap;
          row-gap: 144px;
        }

        &__items {
          display: flex;
          justify-content: space-between;
          gap: 20px;
        }

        &__item {
          flex-basis: 33.333%;
          display: flex;
          align-items: flex-end;
          justify-content: center;

          & img {
            max-width: 100%;
          }

          &:first-child {
            justify-content: start;
          }

          &:last-child {
            justify-content: end;
          }
        }
      }
    }

    &__screens {
      background-color: $nb;
      padding: 143px 122px 226px 121px;
      position: relative;
      & .project5__h2 {
        text-align: center;
      }
      & .pixel {
        &:nth-of-type(1) {
          width: 12%;
          top: 3.3%;
          right: 1.5%;
        }
        &:nth-of-type(2) {
          width: 95%;
          top: 23.7%;
          left: 0;
        }
        &:nth-of-type(3) {
          width: 98%;
          top: 41.2%;
          right: -0.7%;
        }
        &:nth-of-type(4) {
          width: 9%;
          top: 74.3%;
          right: 4%;
        }
        &:nth-of-type(5) {
          width: 83.2%;
          top: 84%;
          right: 2;
        }

      }
      & .project5-screens {
        &__text {
          font-size: 28px;
          line-height: 150%;
          color: #fff;
          padding: 56px 0px 140px 80px;
          max-width: 800px;
        }

        &__block {
          position: relative;
        }

        &__item {
          position: relative;
          & span {
            font-size: 20px;
            line-height: 125%;
            color: #fff;
          }
          & img {
            max-width: 100%;
            position: relative;
            z-index: 1;
          }
        }
        &__hover {
          position: absolute;
            top: 12.1%;
            right: 14.4%;
            width: 18%;
            display: flex;
            align-items: center;

            & img {
              max-width: 100%;
            }

            & span {
              color: #fff;
              position: relative;
              right: -62px;
              top: 50px;
            }
        }
        &__item:nth-of-type(1) {
          &>img {
            max-width: 100%;

            &:nth-of-type(1) {
              width: 62.5%;
              box-shadow: 0px 3.04167px 30.4167px rgba(0, 20, 36, 0.1);
            }

            &:nth-of-type(2) {
              position: absolute;
              width: 62.5%;
              top: 11.5%;
              left: 63px;
            }

            &:nth-of-type(3) {
              position: absolute;
              width: 84%;
              bottom: 19.4%;
              left: 4%;
            }

            &:nth-of-type(4) {
              position: absolute;
              width: 35.1%;
              top: 18.5%;
              right: -4%;
            }
          }
        }

        &__item:nth-of-type(2) {
          margin-top: 263px;
          display: flex;
          z-index: 1;
          justify-content: flex-end;

          & span {
            position: absolute;
            top: 165px;
            width: calc(100% + 244px);
            height: 167px;
            background-color: rgba(103, 53, 255, 0.5);
            z-index: -1;
            display: flex;
            align-items: center;
            padding-left: 19%;
            margin-right: -122px;
          }

          &>img {

            &:nth-child(2) {
              width: 62.5%;
              box-shadow: 0px 3.04167px 30.4167px rgba(0, 20, 36, 0.1);
            }

            &:nth-child(3) {
              position: absolute;
              top: -14%;
              left: 0;
              width: 58%;
            }

            &:nth-child(4) {
              position: absolute;
              bottom: 16.1%;
              left: 15%;
              width: 26.6%;
            }

            &:nth-child(5) {
              position: absolute;
              bottom: 0%;
              left: 15%;
              width: 29.9%;
            }
          }
        }

        &__item:nth-of-type(3) {
          margin-top: 246px;
          z-index: 1;

          & span {
            position: absolute;
            top: 12%;
            width: calc(100% + 244px);
            height: 37%;
            background-color: rgba(103, 53, 255, 0.5);
            z-index: -1;
            display: flex;
            align-items: center;
            justify-content: end;
            padding-right: 20%;
            margin-left: -122px;
            z-index: -1;
          }

          &>img {
            &:nth-child(2) {
              width: 62.5%;
              box-shadow: 0px 3.04167px 30.4167px rgba(0, 20, 36, 0.1);
            }

            &:nth-child(3) {
              position: absolute;
              top: 19%;
              right: 31.5%;
              width: 15%;

            }
          }
        }

        &__item:nth-of-type(4) {
          margin-top: 164px;
          z-index: 1;
          display: flex;
          justify-content: flex-end;

          & span {
            position: absolute;
            top: 9%;
            width: calc(100% + 244px);
            height: 15%;
            background-color: rgba(103, 53, 255, 0.5);
            z-index: -1;
            display: flex;
            align-items: center;
            padding-left: 19%;
            margin-right: -122px;
            z-index: -1;
          }

          &>img {
            &:nth-child(2) {
              box-shadow: 0px 3.04167px 30.4167px rgba(0, 20, 36, 0.1);
              width: 62.5%;
            }

            &:nth-child(3) {
              position: absolute;
              bottom: 0;
              left: 3%;
              width: 25%;
            }
          }
        }

        &__plus {
          margin-top: 176px;
          display: flex;
          align-items: flex-end;
          padding-left: 275px;
          position: relative;
          & p {
            font-size: 52px;
            line-height: 121%;
            color: #fff;
            max-width: 220px;
            text-align: center;
            padding-bottom: 20px;
          }

          & div {
            position: relative;
            left: -25px;

            & img {
              max-width: 100%;
            }
          }
        }
      }
    }

    &__adaptiv {
      background-color: #F4F6FA;

      & .project5 {
        &-adaptiv {
          &__img {
            position: relative;

            & img {
              max-width: 100%;
            }
          }

          &__title {
            position: absolute;
            top: 33%;
            left: 50%;
            transform: translateX(-50%);

            & h2 {
              text-align: center;
            }
          }

          &__ampersand {
            display: flex;
            justify-content: center;
            position: relative;
            margin-top: -272px;

            & span {
              font-weight: 600;
              font-size: 76px;
              line-height: 122%;
              display: flex;
            }

            & span:first-child {
              color: #fff;
              margin-top: 136px;
            }

            & span:last-child {
              color: $nb;
              margin-top: 261px;
            }
          }

          &__text {
            max-width: 692px;
            font-size: 28px;
            line-height: 150%;
            margin: -14px 0px 120px 200px;
          }

          &__block {
            padding: 0px 122px;
            position: relative;
            & span {
              position: absolute;
              background-color: #6735FF;
              transition: .6s;
              &:nth-of-type(1) {
                height: 3%;
                width: 50%;
                right: 0;
                top: 2%;
              }
              &:nth-of-type(2) {
                height: 9%;
                width: 86%;
                left: 0;
                top: 18%;
              }
              &:nth-of-type(3) {
                height: 5%;
                width: 69%;
                right: 4%;
                top: 43.9%;
              }
              &:nth-of-type(4) {
                height: 4%;
                width: 35%;
                right: 16%;
                top: 58%;
              }
              &:nth-of-type(5) {
                height: 4%;
                width: 22%;
                left: 27%;
                top: 62%;
              }
            }
            & img {
              max-width: 100%;
              box-shadow: 0px 3.04167px 30.4167px rgba(0, 20, 36, 0.07);
            }
          }

          &__items:nth-of-type(1) {
            display: flex;
            justify-content: space-between;
            margin-bottom: 126px;
            position: relative;
            z-index: 1;
            & img {
              height: 100%;
            }

            & img:nth-child(1) {
              width: 48%;
            }

            & img:nth-child(2) {
              width: 20%;
            }

            & img:nth-child(3) {
              width: 20%;
            }
          }

          &__items:nth-of-type(2) {
            padding-bottom: 73px;
            display: flex;
            gap: 37px;
            position: relative;
            z-index: 1;
            &::after {
              content: '';
              position: absolute;
              width: calc(100% + 244px);
              height: 264px;
              bottom: 0;
              left: -124px;
              background-image: url(../img/project5/adaptiv/pixel.svg);
              background-repeat: no-repeat;
              background-size: cover;
            }
          }

          &__item:nth-child(1) {
            width: 50%;
            display: flex;
            gap: 60px;
            
            & img:first-child {
              width: 41.6%;
              max-height: 3116px;
            }

            & .project5-adaptiv__item-m {
              width: 41.6%;
              display: flex;
              flex-direction: column;
              gap: 60px;

              & img:first-child {
                width: 100% !important;
                max-height: 1665px;
              }
            }
          }

          &__item:nth-child(2) {
            margin-top: -200px;
            width: 50%;

            & img:nth-child(1) {
              margin-bottom: 156px;
            }

            & img:nth-child(3) {
              box-shadow: none;
              margin-top: -154px;
              margin-left: 44px;
            }

            & .project5-adaptiv__item-m {
              display: flex;
              gap: 60px;
              margin-top: -100px;
              margin-left: -20px;

              & img:nth-child(1) {
                margin-bottom: 0;
                width: 40%;
              }

              & img:nth-child(2) {
                width: 40%;
                margin-top: 58px;
              }
            }
          }
        }


      }

    }

  }
}
@media (max-height: 1192px) and (max-width: 1920px)  {
  .project5 {
    & .project {
      &-hero {
        height: 1000px;
        background-size: cover;
        &__block {
          padding-left: 50px;
          padding-top: 200px;
        }
        &__title {
          font-size: 160px;
        }
      }
      &-main {
        margin-top: 1000px;
      }
    }
  }
}
@media (max-height: 1192px) and (max-width: 1620px) {
  .project5 {
    & .project {
      &-hero {
        background-size: 1620px auto;
    }
  }
}
}
@media (max-height: 1000px) and (max-width: 1920px)  {
  .project5 {
    & .project {
      &-hero {
        height: 900px;
      }
      &-main {
        margin-top: 900px;
      }
    }
  }
}
@media (max-width: 1600px) {
  .project5 {
    &__h2 {
      font-size: 120px;
    }
    & .project {
      &-hero {
        &__title {
          font-size: 160px;
        }

        &__sub-title {
          font-size: 80px;
        }
      }

      &-main {
        &__wp {
          grid-template-columns: 400px 1fr;
        }
      }
    }

    &-sect {
      &__aboutUs {
        & .project5-aboutUs {
          &__block {
            p {
              margin-left: 0;
            }
          }
        }
      }
      &__font {
        & .project5-font {
          &__left {
            & div {
              font-size: 66px;
            }
            & ul li {
              font-size: 26px;
            }
          }

          &__right {
              & span {
                padding-top: 48px;
                font-size: 220px;
              }

            & p {
              margin-top: 113px;
              font-size: 36px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 1480px) {
  .project5 {
    & .project-main__sect:nth-child(6) .project-main__arr {
      left: 36%;
    }
  }
}
@media (max-width: 1366px) {
  .project5 {
    & .project-main__wp {
      grid-template-columns: 1fr;
    }
    & .project-main__left {
      grid-row: 2/3;
      &-link:nth-child(2),
      &-link:nth-child(3) {
        display: none;
      }
    }
    & .project-main__sect:nth-child(6) {
      & .project-main__sect-wp {
        margin-left: 120px;
      }
      & .project-main__arr {
        margin-left: 120px;
        left: -90px;
        bottom: -20px;
      }
    } 
    &-sect__aboutUs {
      grid-row: 1/2;
    }
    &-sect {
      &__aboutUs {
        & .project5-aboutUs {
          &__block p {
            margin-left: 20px;
          }
        }
      }
      &__font {
        & .project5-font {
          &__block {
            padding: 150px 60px 250px 100px;
          }
          &__right {
            & p {
              margin-top: 122px;
            }
          }
        }
      }
    }
  }

}

@media (max-width: 1300px) {
  .project5 {
    & .project {
      &-hero {
        height: 1000px;
        background-size: 1450px auto;

        &__title {
          font-size: 120px;
        }

        &__sub-title {
          font-size: 60px;
        }
      }

      &-main {
        margin-top: 1000px;

      }
    }

    &-sect {
      &__aboutUs {
        & .aboutUs {
          &__fon {
            background-size: cover;
          }
        }
      }
    }
  }
}
@media (max-height: 950px) and (max-width: 1300px)  {
  .project5 {
    & .project {
      &-hero {
        height: 850px;
      }
      &-main {
        margin-top: 850px;
      }
    }
  }
}
@media (max-width: $desktop) {
  .project5 {
    & .project {
      &-hero {
        height: 800px;

        &__block {
          padding-left: 50px;
          padding-top: 200px;
        }
      }

      &-main {
        margin-top: 800px;
      }
    }
    &-sect {
      &__aboutUs {
        padding-top: 100px;
        padding-bottom: 400px;
        & .project5-aboutUs {
          &__block {
            & p {
              margin-top: 40px;
              font-size: 20px;
              max-width: 450px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .project5 {
    & .project {
      &-hero {
        height: 670px;
        background-size: 1100px auto;
        &__title {
          font-size: 86px;
        }

        &__sub-title {
          font-size: 50px;
        }
      }

      &-main {
        margin-top: 700px;
        &__sect {
          &:nth-child(5) {
            padding-top: 65px;
            padding-bottom: 66px;
            & .left {
              min-height: 130px;
              height: auto;
            }
          }
          &-wp {
            .right {
              margin-top: 29px;
            }
          }
        }
        
      }
    }
    &-sect {
      &__aboutUs {
        padding-top: 59px;
        & .project5-aboutUs {
          
          &__block {
            & p {
              margin-top: 47px;
              font-size: 20px;
              max-width: 450px;
            }
