.works {


    &-hero {
        padding-top: 118px;
        padding-bottom: 61px;
        background-image: url(../img/main/main-back.png);
        background-position: left top;
        background-size: cover;
        background-repeat: no-repeat;

        &__wp {
            display: flex;
            flex-direction: column;
            align-items: center;

            h2 {
                margin-top: 13px;
            }

            @media (max-width: 760px) {
                svg {
                    width: 18px;
                }

                h2 {
                    margin-top: 5px;
                }
            }
        }


        @media (max-width: 990px) {
            padding-top: 174px;
            padding-bottom: 49px;
        }

        @media (max-width: 760px) {
            padding-top: 82px;
            padding-bottom: 27px;
        }
    }

    &-main {
        padding-bottom: 31px;
        max-width: 1920px;
        width: 100%;
        margin: auto;
        padding: 0px 60px;

        @media (max-width: 1440px) {
            padding: 0px 32px;
        }

        @media (max-width: 768px) {
            padding: 0px 16px;
        }

        &__top {
            padding-top: 24px;
            display: flex;
            justify-content: space-between;

            &-right {
                display: flex;
                align-items: center;
                // justify-content: flex-end;
                width: calc(100% - 225px);

                .swiper-wrapper {
                    justify-content: flex-end;
                }

                a {
                    height: 48px;
                    font-style: normal;
                    font-size: 14px;
                    line-height: 140%;
                    padding: 14px 23px;
                    border: 1px solid transparent;
                    box-sizing: border-box;
                    width: auto;
                    white-space: nowrap;

                    &.active {
                        font-weight: 600;
                        color: #EF7931;
                        background: $text-color;
                    }

                    @media (min-width: 1141px) {
                        &:hover {
                            border-color: #242B2F;
                        }
                    }

                    @media (max-width: 760px) {
                        width: auto;
                        font-size: 12px;
                        line-height: 140%;
                        padding: 11px 15px;
                        height: 40px;
                        white-space: nowrap;
                    }
                }

                @media (max-width: 990px) {
                    margin-top: 24px;
                    width: 100%;

                    .swiper-wrapper {
                        justify-content: center;
                    }
                }

                @media (max-width: 760px) {
                    width: 100vw;
                    margin-top: 16px;
                    padding-left: 16px;
                    padding-right: 16px;
                }
            }

            &-left {
                line-height: 0px;
            }

            @media (max-width: 990px) {
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;

                .simple-select {
                    height: 48px;

                    &:before {
                        right: 17px;
                        top: 16px;
                        width: 7px;
                        height: 7px;
                    }

                    &__main {
                        padding: 10px 15px 11px 24px;
                    }

                    &__selected {
                        font-size: 14px;
                        line-height: 140%;
                    }
                }
            }

            @media (max-width: 760px) {
                padding-top: 16px;

                .simple-select {
                    height: 40px;
                    width: 194px;

                    &:before {
                        right: 16px;
                        top: 11px;
                        width: 8px;
                        height: 8px;
                    }

                    &__main {
                        padding-left: 15px;
                        padding-right: 12px;
                    }

                    &__selected {
                        font-size: 12px;
                        line-height: 14px;
                    }
                }
            }

        }

        &__item {
            background: #FFFFFF;
            transition: 0.25s;
            border-radius: 0 0 8px 8px;
            flex-basis: 52%;
            position: relative;
            max-height: 758px;
            color: #242B2F;

            &-color {
                color: #fff;
            }

            &-title {
                letter-spacing: 0.02em;
                font-size: 40px;
                line-height: 140%;
                font-weight: 600;
                position: absolute;
                top: 6.3%;
                left: 6.5%;
            }

            &-text {
                top: 14.8%;
                left: 6.5%;
                position: absolute;
                font-size: 24px;
                line-height: 140%;
                font-weight: 300;
                padding-right: 10px;
            }

            &-img {
                min-height: 100%;
                display: flex;

                & img {
                    width: 100%;
                    aspect-ratio: 980/758;
                }
            }

            &-soon {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                display: flex;
                background: rgba(255, 255, 255, 0.75);

                & img,
                svg {
                    max-width: 56px;
                    max-height: 218px;
                }

                @media (max-width: 1900px) {
                    justify-content: end;

                    & img,
                    svg {
                        max-width: 56px;
                        max-height: 218px;
                        margin-right: 5%;
                    }
                }

                @media (max-width: $desktop) {

                    & img,
                    svg {
                        height: 150px;
                    }
                }

                @media (max-width: $tablet) {

                    & img,
                    svg {
                        height: 93.5px;
                        width: 24px;
                    }
                }

                @media (max-width: 360px) {

                    & img,
                    svg {
                        height: 77.92px;
                        width: 20px;
                    }
                }
            }

            @media (max-width: 1640px) {
                flex-basis: 51.5%;

                &-title {
                    font-size: 36px;
                }

                &-text {
                    font-size: 20px;
                }
            }

            @media (max-width: 1366px) {
                &-title {
                    top: 4%;
                }

                &-text {
                    top: 12.8%;
                }
            }

            @media (max-width: $desktop) {
                &-title {
                    font-size: 30px;
                    top: 3.5%;
                }

                &-text {
                    font-size: 18px;
                }
            }

            @media (max-width: $tablet) {
                &-title {
                    font-size: 22px;
                }

                &-text {
                    font-size: 12px;
                }
            }

            @media (max-width: 768px) {
                flex-basis: 52.7%;

                &-title {
                    font-size: 16px;
                    top: 3.5%;
                }
            }

            @media (max-width: 700px) {
                flex-basis: 100%;

                &-title {
                    font-size: 20px;
                }

                &-text {
                    top: 10%;
                    font-size: 14px;
                }
            }

            @media (max-width: $mobile) {
                &-title {
                    font-size: 16px;
                }

                &-text {
                    font-size: 12px;
                }
            }

            @media (max-width: $mobile-sm) {
                &-title {
                    top: 5.1%;
                    left: 5.5%;
                }

                &-text {
                    top: 16.1%;
                    left: 5.5%;
                    font-size: 10px;
                }
            }
        }

        &__grid {
            display: flex;
            flex-wrap: wrap;
            gap: 16px;
            margin: 40px -44px 0px;
            padding-bottom: 120px;

            & *:nth-child(4n + 2) {
                flex-basis: 47%;
            }

            & *:nth-child(4n + 3) {
                flex-basis: 47%;
            }

            @media (max-width: 1440px) {
                margin: 40px -20px 0px;
            }

            @media (max-width: 1100px) {
                padding-bottom: 80px;

                & *:nth-child(4n + 3) {
                    flex-basis: 46.5%;
                }

                & *:nth-child(4n + 2) {
                    flex-basis: 46.5%;
                }
            }

            @media (max-width: 991px) {
                & *:nth-child(4n + 3) {
                    flex-basis: 46%;
                }

                & *:nth-child(4n + 2) {
                    flex-basis: 46%;
                }
            }

            @media (max-width: 768px) {
                margin: 32px -8px 0px;
                gap: 8px;
                padding-bottom: 56px;
            }

            @media (max-width: 700px) {

                & *:nth-child(4n + 3) {
                    flex-basis: 100%;
                }

                & *:nth-child(4n + 2) {
                    flex-basis: 100%;
                }
            }
        }

        @media (max-width: 990px) {
            padding-bottom: 34px;
        }

        @media (max-width: 760px) {
            padding-bottom: 30px;
        }

    }
}
